import { Component, EventEmitter, Input, Output, TemplateRef } from '@angular/core';
import { IBreadcrumb } from '../../interfaces/components/breadcrumb.interface';
import { NgClass } from '@angular/common';
import { Router } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';

@Component({
  selector: 'custom-breadcrumb',
  imports: [NgClass, TranslateModule],
  templateUrl: './custom-breadcrumb.component.html',
  styleUrl: './custom-breadcrumb.component.css',
})
export class CustomBreadcrumbComponent {
  @Input() actionsTemplate?: TemplateRef<unknown>;
  @Input({ required: true }) breadcrumbItems: Array<IBreadcrumb> = [];
  @Input() saveBtnLabel: string = 'GENERAL.SAVE';
  @Input() cancelBtnLabel: string = 'GENERAL.CANCEL';
  @Input() saveBtnDisabled: boolean = true;
  @Output() saveBtnClicked = new EventEmitter<void>();
  @Output() cancelBtnClicked = new EventEmitter<void>();
  @Output() breadcrumbItemClicked: (item: IBreadcrumb) => void = (
    item: IBreadcrumb
  ) => {
    if(item.url){
      this.router.navigate([item.url]);
    }
  };

  constructor(private router: Router) {}
}
