import { Injectable } from '@angular/core';
import { OverlayPanelComponent } from './overlay-panel.component';

@Injectable({ providedIn: 'root' })
export class OverlayManagerService {
  private currentlyOpen: OverlayPanelComponent | null = null;

  register(panel: OverlayPanelComponent) {
    if (this.currentlyOpen && this.currentlyOpen !== panel) {
      this.currentlyOpen.closeDropdown();
    }
    this.currentlyOpen = panel;
  }

  unregister(panel: OverlayPanelComponent) {
    if (this.currentlyOpen === panel) {
      this.currentlyOpen = null;
    }
  }
}
