import { CommonModule } from '@angular/common';
import { Component, Input, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'custom-title-content',
  imports: [CommonModule],
  templateUrl: './custom-title-content.component.html',
  styleUrl: './custom-title-content.component.css',
  encapsulation: ViewEncapsulation.None, // disables style encapsulation
})
export class CustomTitleContentComponent {
  @Input() title!: string;
  @Input() color: string = '#06213D';
  @Input() fontSize: string = '1.25em';
  @Input() titlePadding: string = '0 0 0.2em 0';
  @Input() titleMargin: string = '0 0 1em 0';
  @Input() separator = true;
}
