import { Component, EventEmitter, Input, Output, signal } from '@angular/core';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { FormGroup, FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IComponentFormError } from '../../interfaces';
import { hours, minutes } from './time-consts';
import { ClickOutsideDirective } from '../../directives';
import { NgStyle } from '@angular/common';
import { ComponentFormErrorConstant } from '../../enums';

@Component({
  selector: 'custom-time-input-form',
  imports: [
    NgStyle,
    CustomAppErrorComponent,
    ReactiveFormsModule,
    FormsModule,
    ClickOutsideDirective,
  ],
  templateUrl: './custom-time-input-form.component.html',
  styleUrl: './custom-time-input-form.component.css',
})
export class CustomTimeInputFormComponent {
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) name: string = '';
  @Input() controlName: string = '';
  @Input() label: string = '';
  @Input() labelClass: string = '';
  @Input() inputClass: string = '';
  @Input() validation: IComponentFormError[] = [];
  @Input() defaultTime: string = ''; // e.g. '09:00 AM'
  @Output() timeChange = new EventEmitter<{ hour: number; minute: number }>();
  @Input() height: string = '4.8em';
  @Input() rangeMin: string = ''; // e.g. '08:00'
  @Input() rangeMax: string = ''; // e.g. '18:00'

  dropdownOpen = signal<boolean>(false);
  hours = hours;
  minutes = minutes;
  selectedHour!: number | string;
  selectedMinute!: number | string;
  selectedPeriod: 'AM' | 'PM' = 'AM'; // 'AM' |'PM'

  get filteredHours(): number[] {
    if (!this.rangeMin && !this.rangeMax) {
      return this.hours;
    }

    return this.hours.filter((h) => {
      // Convert 12-hour to 24-hour format for comparison
      let hour24 = h;
      if (this.selectedPeriod === 'PM' && h !== 12) {
        hour24 = h + 12;
      } else if (this.selectedPeriod === 'AM' && h === 12) {
        hour24 = 0;
      }

      if (this.rangeMin) {
        const [minH] = this.rangeMin.split(':').map(Number);
        if (hour24 < minH) return false;
      }

      if (this.rangeMax) {
        const [maxH] = this.rangeMax.split(':').map(Number);
        if (hour24 > maxH) return false;
      }

      return true;
    });
  }

  get filteredMinutes(): number[] {
    if ((!this.rangeMin && !this.rangeMax) || !this.selectedHour) {
      return this.minutes;
    }

    // Convert current hour to 24-hour format
    let hour24 = Number(this.selectedHour);
    if (this.selectedPeriod === 'PM' && hour24 !== 12) {
      hour24 = hour24 + 12;
    } else if (this.selectedPeriod === 'AM' && hour24 === 12) {
      hour24 = 0;
    }

    return this.minutes.filter((m) => {
      if (this.rangeMin) {
        const [minH, minM] = this.rangeMin.split(':').map(Number);
        if (hour24 === minH && m < minM) return false;
      }

      if (this.rangeMax) {
        const [maxH, maxM] = this.rangeMax.split(':').map(Number);
        if (hour24 === maxH && m > maxM) return false;
      }

      return true;
    });
  }

  ngOnInit() {
    if (this.defaultTime) {
      this.setTimeFromString(this.defaultTime);
    }
    const control = this.parentForm.get(this.controlName);
    if (control?.value) {
      this.setExcistValue();
    } else {
      this.setFormValue();
    }
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }

  toggleDropdown() {
    this.dropdownOpen.set(!this.dropdownOpen());
    const control = this.parentForm.get(this.controlName);
    if (control?.value) {
      this.setExcistValue();
    } else {
      this.setFormValue();
    }

    // Auto-select single options when opening dropdown
    if (this.dropdownOpen()) {
      this.autoSelectSingleOptions();
    }

    if (
      this.parentForm.get(this.controlName)?.invalid &&
      !this.dropdownOpen()
    ) {
      this.parentForm.get(this.controlName)?.markAsTouched();
    }

    if (!this.dropdownOpen()) {
      this.confirmTime();
      this.setFormValue();
      this.autoSelectZeroMinutes();
    }
  }

  onHourChange() {
    // Trigger filteredMinutes recalculation by accessing it
    console.log('Hour changed to: ', this.selectedHour);
    this.autoSelectSingleOptions();
    const mins = this.filteredMinutes;
    // If only one minute option and not already selected, auto-select it
    if (mins.length === 1 && this.selectedMinute !== mins[0].toString()) {
      this.selectedMinute = mins[0].toString();
    }
    // Also check if selected minute is no longer valid
    if (!mins.includes(Number(this.selectedMinute))) {
      this.selectedMinute = mins.length === 1 ? mins[0].toString() : '';
    }
  }

  autoSelectZeroMinutes() {
    // Auto-select minute to 0 if not selected on toggle close
    if (
      (this.selectedMinute === '' || this.selectedMinute === undefined) &&
      this.filteredMinutes.includes(0)
    ) {
      this.selectedMinute = '0';
    }
  }

  autoSelectSingleOptions() {
    // Auto-select hour if only one option

    if (this.filteredHours.length === 1 && this.selectedHour !== hours[0]) {
      this.selectedHour = hours[0];
    }

    // Auto-select minute if only one option
    // const mins = this.filteredMinutes;
    console.log('this.filteredMinutes: ', this.filteredMinutes);
    if (
      this.filteredMinutes.length === 1 &&
      this.selectedMinute !== this.filteredMinutes[0].toString()
    ) {
      console.log(
        'this.selectedMinute: ',
        this.selectedMinute,
        this.filteredMinutes[0]
      );
      this.selectedMinute = this.filteredMinutes[0];
      this.setFormValue();
      console.log('this.selectedMinute: ', this.selectedMinute);
    }
  }

  setExcistValue() {
    console.log('selectedHour: ', this.selectedHour);
    const timeString = this.parentForm.get(this.controlName)?.value; // e.g., "13:45:00"
    const [hour, minute] = timeString.split(':');

    this.selectedHour = Number(hour);
    if (this.selectedHour > 12) {
      this.selectedHour = this.selectedHour - 12;
      this.selectedPeriod = 'PM';
    }
    if (this.selectedHour == 12) {
      this.selectedPeriod = 'PM';
    }
    if (hour == '00') {
      this.selectedHour = '00';
      this.selectedPeriod = 'AM';
    }
    this.selectedMinute = Number(minute);
  }

  setFormValue() {
    let targetHour = this.selectedHour;
    let taregtMin = this.selectedMinute;
    if (this.selectedHour && this.selectedMinute) {
      if (Number(this.selectedHour) < 10)
        targetHour = `0${Number(this.selectedHour)}`;
      if (Number(this.selectedMinute) < 10)
        taregtMin = `0${Number(this.selectedMinute)}`;

      this.parentForm
        .get(this.controlName)
        ?.setValue(`${targetHour}:${taregtMin}:00`);
      //   this.parentForm.get(this.controlName)?.setValue({
      //   hour: Number(this.selectedHour) ?? 0,
      //   minute:Number(this.selectedMinute)  ?? 0,
      //   second: 0,
      //   nano: 0,
      // });
    } else {
      this.parentForm.get(this.controlName)?.setValue(null);
    }
  }

  confirmTime() {
    this.dropdownOpen.set(false);
    let h = Number(this.selectedHour);
    const m = Number(this.selectedMinute);
    if (this.selectedPeriod == 'PM' && this.selectedHour != 12) {
      this.selectedHour = Number(this.selectedHour) + 12;
    }

    if (this.selectedPeriod == 'AM' && this.selectedHour == 12) {
      this.selectedHour = '00';
    }

    // Validate against min/max range
    if (!this.isTimeInRange()) {
      this.parentForm.get(this.controlName)?.setErrors({ timeRange: true });
      this.parentForm.get(this.controlName)?.markAsTouched();
      return;
    }
    // const timeDetails: ITimeDetails = {
    //   hour: Number(this.selectedHour) ?? 0,
    //   minute: Number(this.selectedMinute) ?? 0,
    //   second: 0,
    //   nano: 0,
    // };
    // console.log('timeDetails: ', timeDetails);
    this.timeChange.emit({ hour: Number(this.selectedHour), minute: m });
    this.setFormValue();
  }

  private setTimeFromString(timeStr: string) {
    const match = timeStr.match(/^(\d{1,2}):(\d{2})\s*(AM|PM)$/i);
    if (match) {
      this.selectedHour = parseInt(match[1], 10);
      this.selectedMinute = parseInt(match[2], 10);
      this.selectedPeriod = match[3].toUpperCase() as 'AM' | 'PM';
    }
  }

  displayTime(): string | null {
    return (
      `${this.selectedHour?.toString().padStart(2, '0') || '--'}:${
        this.selectedMinute?.toString().padStart(2, '0') || '--'
      } ` || null
    );
    //${this.selectedPeriod}
    // return {
    //   hour: this.selectedHour ?? 0,
    //   minute: this.selectedMinute ?? 0,
    //   second: 0,
    //   nano: 0,
    // };
  }

  private isTimeInRange(): boolean {
    if (!this.rangeMin && !this.rangeMax) {
      return true; // No range restrictions
    }

    const selectedTime = this.getTimeInMinutes(
      Number(this.selectedHour),
      Number(this.selectedMinute)
    );

    if (this.rangeMin) {
      const [minH, minM] = this.rangeMin.split(':').map(Number);
      const minTime = this.getTimeInMinutes(minH, minM);
      if (selectedTime < minTime) {
        return false;
      }
    }

    if (this.rangeMax) {
      const [maxH, maxM] = this.rangeMax.split(':').map(Number);
      const maxTime = this.getTimeInMinutes(maxH, maxM);
      if (selectedTime > maxTime) {
        return false;
      }
    }

    return true;
  }

  private getTimeInMinutes(hour: number, minute: number): number {
    return hour * 60 + minute;
  }
}
