import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { IComponentFormError } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { CommonModule } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';
import { registerLocaleData } from '@angular/common';
import localeAr from '@angular/common/locales/ar';
import localeEn from '@angular/common/locales/en';

registerLocaleData(localeAr);
registerLocaleData(localeEn);

@Component({
  selector: 'custom-calender-open-form',
  standalone: true,
  imports: [CustomAppErrorComponent, ReactiveFormsModule, CommonModule],
  templateUrl: './custom-calender-open-form.component.html',
  styleUrls: ['./custom-calender-open-form.component.css'],
})
export class CustomCalenderOpenFormComponent {
  // TODO: implement ControlValueAccessor properly
  // Inputs
  @Input() label: string = '';
  @Input() placeholder: string = 'Select date';
  @Input() minDate: Date | null = null;
  @Input() maxDate: Date | null = null;
  @Input({ required: true }) controlName: string = '';
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input({ required: true }) name: string = '';
  @Input() disabled: boolean = false;
  @Output() valueChange = new EventEmitter<Date | null>();
  // Custom classes
  @Input() calendarPopUpClass: string = '';
  @Input() calendarInputClass: string = '';
  @Input() calendarContainerClass: string = '';
  @Input() componentClass: string = '';
  @Input() filterDesign: boolean = false;
  @Input() labelClass: string = '';

  // Calendar state
  showCalendarForm: boolean = false;
  currentMonth: Date = new Date();
  days: Date[] = [];
  today = new Date();

  // ControlValueAccessor implementation
  private _value: Date | null = null;
  private onChange: any = () => {};
  private onTouched: any = () => {};

  months: { value: number; label: string }[] = [];
  years: number[] = [];
  selectedMonth: number | null = null;
  selectedYear: number | null = null;

  constructor(private translationService: TranslateService) {
    this.initializeMonthsAndYears();
    this.generateCalendar();
  }

  ngOnInit(): void {
    if (this.parentForm && this.controlName) {
      const control = this.parentForm.get(this.controlName);
      if (control) {
        this.value = control.value;
        control.valueChanges.subscribe((value: any) => {
          this.value = value;
        });
      }
    }
  }

  private initializeMonthsAndYears(): void {
    // Generate years array (e.g., current year - 100 to current year + 10)
    const currentYear = new Date().getFullYear();
    const startYear = currentYear - 100;
    const endYear = currentYear + 10;

    this.years = [];
    for (let year = startYear; year <= endYear; year++) {
      this.years.push(year);
    }

    this.updateMonthLabels();
  }

  private updateMonthLabels(): void {
    const lang = this.translationService.currentLang || 'en';
    const locale = lang === 'ar' ? 'ar-EG' : 'en-US';

    this.months = [];
    for (let i = 0; i < 12; i++) {
      const date = new Date(2000, i, 1);
      const label = date.toLocaleString(locale, { month: 'long' });
      this.months.push({ value: i, label });
    }
  }

  onMonthChange(event: Event): void {
    const selectElement = event.target as HTMLSelectElement;
    const newMonth = parseInt(selectElement.value, 10);

    if (!isNaN(newMonth)) {
      this.selectedMonth = newMonth;

      // If year is already selected, update the calendar
      if (this.selectedYear !== null) {
        this.updateCalendarFromDropdowns();
      }
    }
  }

  onYearChange(event: Event): void {
    const selectElement = event.target as HTMLSelectElement;
    const newYear = parseInt(selectElement.value, 10);

    if (!isNaN(newYear)) {
      this.selectedYear = newYear;

      // Update the calendar with both month and year
      if (this.selectedMonth !== null) {
        this.updateCalendarFromDropdowns();
      }
    }
  }

  private updateCalendarFromDropdowns(): void {
    if (this.selectedMonth !== null && this.selectedYear !== null) {
      this.currentMonth = new Date(this.selectedYear, this.selectedMonth, 1);
      this.generateCalendar();

      // Reset selections after updating
      this.selectedMonth = null;
      this.selectedYear = null;
    }
  }

  isToday(day: Date) {
    return (
      day.getDate() === this.today.getDate() &&
      day.getMonth() === this.today.getMonth() &&
      day.getFullYear() === this.today.getFullYear()
    );
  }
  // Update existing navigation methods to reset dropdown selections
  prevMonth(): void {
    this.currentMonth = new Date(
      this.currentMonth.getFullYear(),
      this.currentMonth.getMonth() - 1,
      1
    );
    this.selectedMonth = null;
    this.selectedYear = null;
    this.generateCalendar();
  }

  nextMonth(): void {
    this.currentMonth = new Date(
      this.currentMonth.getFullYear(),
      this.currentMonth.getMonth() + 1,
      1
    );
    this.selectedMonth = null;
    this.selectedYear = null;
    this.generateCalendar();
  }
  // Getters and setters
  get value(): Date | null {
    return this._value;
  }

  set value(val: Date | null) {
    this._value = val;
    this.onChange(val);
    this.onTouched();
  }
  get weekdays(): string[] {
    const lang = this.translationService.currentLang || 'en';

    if (lang === 'ar') {
      return ['', '', '', '', '', '', ''];
    }
    return ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];
  }
  // ControlValueAccessor methods
  writeValue(value: Date | null): void {
    this._value = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  // prevMonth(): void {
  //   this.currentMonth = new Date(
  //     this.currentMonth.getFullYear(),
  //     this.currentMonth.getMonth() - 1,
  //     1
  //   );
  //   this.generateCalendar();
  // }

  // nextMonth(): void {
  //   this.currentMonth = new Date(
  //     this.currentMonth.getFullYear(),
  //     this.currentMonth.getMonth() + 1,
  //     1
  //   );
  //   this.generateCalendar();
  // }

  // Calendar generation
  generateCalendar(): void {
    this.days = [];
    const year = this.currentMonth.getFullYear();
    const month = this.currentMonth.getMonth();

    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);

    // Previous month days
    const prevMonthDays = firstDay.getDay();
    for (let i = prevMonthDays - 1; i >= 0; i--) {
      this.days.push(new Date(year, month, -i));
    }

    // Current month days
    for (let i = 1; i <= lastDay.getDate(); i++) {
      this.days.push(new Date(year, month, i));
    }

    // Next month days
    const nextMonthDays = 6 - lastDay.getDay();
    for (let i = 1; i <= nextMonthDays; i++) {
      this.days.push(new Date(year, month + 1, i));
    }
  }

  // Date selection
  selectDate(date: Date): void {
    const normalized = this.normalizeToMidnightUTC(date);
    this.applyDate(normalized);
  }

  private normalizeToMidnightUTC(date: Date): Date {
    // Create a UTC-based date at 00:00:00 of the selected local date
    const utcTimestamp = Date.UTC(
      date.getFullYear(),
      date.getMonth(),
      date.getDate()
    );
    return new Date(utcTimestamp);
  }

  private applyDate(date: Date): void {
    this._value = date;
    this.showCalendarForm = false;
    this.parentForm.get(this.controlName)?.setValue(date);
    this.parentForm.get(this.controlName)?.markAsTouched();
    this.valueChange.emit(date);
  }

  // Utility methods
  isSelected(date: Date): boolean {
    if (!this.value) return false;
    return (
      date.getDate() === this.value.getDate() &&
      date.getMonth() === this.value.getMonth() &&
      date.getFullYear() === this.value.getFullYear()
    );
  }

  isCurrentMonth(date: Date): boolean {
    return date.getMonth() === this.currentMonth.getMonth();
  }

  isDisabled(date: Date): boolean {
    const isBeforeMin = this.minDate
      ? this.isSameOrBefore(date, this.minDate)
      : false;
    const isAfterMax = this.maxDate
      ? this.isSameOrAfter(date, this.maxDate)
      : false;
    return isBeforeMin || isAfterMax;
  }

  isSameOrBefore(date1: Date, date2: Date): boolean {
    const d1 = new Date(date1.getFullYear(), date1.getMonth(), date1.getDate());
    const d2 = new Date(date2.getFullYear(), date2.getMonth(), date2.getDate());
    return d1.getTime() < d2.getTime();
  }

  isSameOrAfter(date1: Date, date2: Date): boolean {
    const d1 = new Date(date1.getFullYear(), date1.getMonth(), date1.getDate());
    const d2 = new Date(date2.getFullYear(), date2.getMonth(), date2.getDate());
    return d1.getTime() > d2.getTime();
  }

  getMonthName(): string {
    const lang = this.translationService.currentLang || 'en';
    const locale = lang === 'ar' ? 'ar-EG' : 'en-US';
    return this.currentMonth.toLocaleString(locale, { month: 'long' });
  }

  getYear(): number {
    return this.currentMonth.getFullYear();
  }

  formatDisplayDate(): string {
    if (!this.value) return '';
    try {
      console.log(
        this.value.toLocaleDateString('en-GB', {
          day: '2-digit',
          month: '2-digit',
          year: 'numeric',
        })
      );
      return this.value.toLocaleDateString('en-GB', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
      });
    } catch (error) {
      return '';
    }
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }

  dayClick(event: Event, day: Date) {
    event.stopPropagation();
    this.parentForm.get(this.controlName)?.markAsTouched();
    console.log('Day clicked:', day);
    if (!this.isDisabled(day) && !this.disabled) {
      this.selectDate(day);
    }
  }
}
