import {
  Component,
  ComponentRef,
  EventEmitter,
  Injector,
  input,
  Input,
  Output,
  Provider,
  Type,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReplaySubject } from 'rxjs';
import { MODAL_REF, ModalRefApi } from '../../services';

@Component({
  selector: 'modal',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './custom-modal.component.html',
  styleUrls: ['./custom-modal.component.css'],
})
export class CustomModalComponent {
  // Inputs
  @Input() modalTitle = '';
  @Input() modalIcon = '';

  @Input() overlayClickClose = true;
  @Input() showHeader = true;

  // Outputs
  @Output() hideEvent = new EventEmitter<void>();

  @Output() closed = new EventEmitter<void>();

  isVisible = false;

  // Anchor readiness (inside *ngIf*)
  private contentReadySubject = new ReplaySubject<void>(1);
  readonly contentReady$ = this.contentReadySubject.asObservable();

  private hostVcRef!: ViewContainerRef;
  private dynamicChildRef?: ComponentRef<any>;

  // Bind to the same ref name as the template: #contentHost
  // NOTE: do NOT set static: true (its inside *ngIf*)
  @ViewChild('contentHost', { read: ViewContainerRef })
  set contentHost(vcr: ViewContainerRef | null) {
    if (vcr) {
      this.hostVcRef = vcr;
      this.contentReadySubject.next();
    }
  }

  // Public API
  open(): void { this.isVisible = true; }

  close(): void {
    this.isVisible = false;
    this.clearDynamicContent();
    this.closed.emit();
  }

  closeInternal(): void {
    this.isVisible = false;
    this.clearDynamicContent();
    this.hideEvent.emit();
    this.closed.emit();
  }



  onOverlayClick(event: MouseEvent): void {
    if (event.target === event.currentTarget && this.overlayClickClose) this.close();
  }

  /** Attach child and provide MODAL_REF so it can close itself */
  attachContent<T>(component: Type<T>, extraProviders: Provider[] = []): ComponentRef<T> {
    if (!this.hostVcRef) throw new Error('Modal content host not ready');

    const modalRefApi: ModalRefApi = { close: () => this.close() };

    const injector = Injector.create({
      providers: [{ provide: MODAL_REF, useValue: modalRefApi }, ...extraProviders],
      parent: this.hostVcRef.injector,
    });

    this.hostVcRef.clear();
    const ref = this.hostVcRef.createComponent(component, { injector });
    this.dynamicChildRef = ref as ComponentRef<any>;
    return ref;
  }

  // Helpers
  private clearDynamicContent(): void {
    this.hostVcRef?.clear();
    this.dynamicChildRef?.destroy();
    this.dynamicChildRef = undefined;
  }
}
