import { Component, input, Input, SimpleChanges } from '@angular/core';
import {
  FormArray,
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { CustomInputFormComponent } from '../custom-input-form/custom-input-form.component';
import { TranslateModule } from '@ngx-translate/core';
import { ComponentFormErrorConstant } from '../../enums';
import { IComponentFormError } from '../../interfaces';
@Component({
  selector: 'custom-fields-form',
  imports: [ReactiveFormsModule, CustomInputFormComponent, TranslateModule],
  templateUrl: './custom-fields-form.component.html',
  styleUrl: './custom-fields-form.component.css',
})
export class CustomFieldsFormComponent {
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) controlName!: string;
  @Input() addTitle: string = 'Add Custom Field';
  autoPruneEmpty:boolean = true;
  addOneField = input<boolean>(false);

  requiredField: IComponentFormError[] = [
    {
      errorType: [ComponentFormErrorConstant.REQUIRED],
      errorMessage: 'Field is required',
    },
  ];

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    if (!this.parentForm.get(this.controlName)) {
      this.parentForm.addControl(this.controlName, this.fb.array([]));
    }
    this.hydrateFromExistingValue();
    if (this.autoPruneEmpty) this.pruneEmptyGroups();
  }
  ngOnChanges(_: SimpleChanges): void {
    // if parent replaces the control later (e.g. setControl), hydrate again
    this.hydrateFromExistingValue();
    if (this.autoPruneEmpty) this.pruneEmptyGroups();
  }
  private hydrateFromExistingValue(): void {
    const ctrl = this.parentForm.get(this.controlName);

    if (!(ctrl instanceof FormArray)) return;

    const arr = ctrl as FormArray;
    const val = arr.value;

    // Case A: already correct (array of FormGroups) -> do nothing
    if (arr.length > 0 && arr.controls.every((c) => c instanceof FormGroup))
      return;

    // Case B: empty controls but we have raw values -> build groups
    if (Array.isArray(val) && val.length > 0) {
      arr.clear({ emitEvent: false });
      for (const item of val) {
        arr.push(
          this.fb.group({
            key: [item?.key ?? '', Validators.required],
            value: [item?.value ?? '', Validators.required],
          }),
          { emitEvent: false }
        );
      }
    }
  }
    private isEmptyGroup(g: FormGroup): boolean {
    const key = (g.get('key')?.value ?? '').toString().trim();
    const val = (g.get('value')?.value ?? '').toString().trim();
    return !key && !val;
  }

  private pruneEmptyGroups(): void {
    const arr = this.fieldsFormArray;
    for (let i = arr.length - 1; i >= 0; i--) {
      const g = arr.at(i) as FormGroup;
      if (this.isEmptyGroup(g)) {
        arr.removeAt(i, { emitEvent: false });
      }
    }
  }

  get fieldsFormArray(): FormArray {
    return this.parentForm.get(this.controlName) as FormArray;
  }

  get fieldsFormArrayControllers(): FormGroup[] {
    return (this.parentForm.get(this.controlName) as FormArray)
      .controls as FormGroup[];
  }

  addField() {
        if (this.autoPruneEmpty) this.pruneEmptyGroups();

    const group = this.fb.group({
      key: ['', Validators.required],
      value: ['', Validators.required],
    });
    this.fieldsFormArray.push(group);
  }

  removeField(index: number) {
    this.fieldsFormArray.removeAt(index);
  }
}
