// confirm-dialog.service.ts
import {
  ApplicationRef,
  ComponentRef,
  EnvironmentInjector,
  Injectable,
  createComponent,
  inject,
} from '@angular/core';
import { firstValueFrom, Subject, take } from 'rxjs';
import { ConfirmOptions, ConfirmResult, CustomConfirmPopupComponent } from '../../components';

@Injectable({ providedIn: 'root' })
export class ConfirmDialogService {
  private readonly applicationRef = inject(ApplicationRef);
  private readonly environmentInjector = inject(EnvironmentInjector);

  async confirm(options: ConfirmOptions): Promise<ConfirmResult> {
    const hostElement = this.createHostElement();
    const componentRef = this.mountComponent(hostElement);

    try {
      this.setComponentInputs(componentRef, options);
      const decision = await this.waitForUserDecision(componentRef, options);
      return decision;
    } finally {
      this.destroyComponent(componentRef, hostElement);
    }
  }

  //  helpers 

  private createHostElement(): HTMLElement {
    const hostElement = document.createElement('div');
    hostElement.classList.add('confirm-dialog-host'); // optional: z-index or layout hook
    document.body.appendChild(hostElement);
    return hostElement;
  }

  private mountComponent(hostElement: HTMLElement): ComponentRef<CustomConfirmPopupComponent> {
    const componentRef = createComponent(CustomConfirmPopupComponent, {
      environmentInjector: this.environmentInjector,
      hostElement,
    });
    this.applicationRef.attachView(componentRef.hostView);
    return componentRef;
  }

  private destroyComponent(
    componentRef: ComponentRef<CustomConfirmPopupComponent>,
    hostElement: HTMLElement
  ): void {
    this.applicationRef.detachView(componentRef.hostView);
    componentRef.destroy();
    hostElement.remove();
  }

  private setComponentInputs(
    componentRef: ComponentRef<CustomConfirmPopupComponent>,
    options: ConfirmOptions
  ): void {
    const instance = componentRef.instance;
    // instance.type = options.type ?? 'info';
    // instance.message = options.message;
    // instance.modalWarningMessage = options.extraText ?? '';
    // instance.modalTitle = options.modalTitle;
    // instance.modalIcon = options.modalIcon;
    instance.confirmButtonText = options.confirmText ?? 'GENERAL.YES';
    instance.cancelButtonText = options.cancelText ?? 'GENERAL.NO';
    instance.open();
  }

  private async waitForUserDecision(
    componentRef: ComponentRef<CustomConfirmPopupComponent>,
    options: ConfirmOptions
  ): Promise<ConfirmResult> {
    const instance = componentRef.instance;
    const decisionSubject = new Subject<ConfirmResult>();

    // Confirm
    instance.confirmEvent.pipe(take(1)).subscribe(() => {
      decisionSubject.next('confirm');
      decisionSubject.complete();
    });

    // Cancel
    instance.cancelEvent.pipe(take(1)).subscribe(() => {
      decisionSubject.next('cancel');
      decisionSubject.complete();
    });



    return firstValueFrom(decisionSubject);
  }
}
