import { Component, ContentChild, Input, TemplateRef } from '@angular/core';
import {
  ClickOutsideDirective,
  dropdownAnimation,
  DropdownsAnimationDirective,
} from '../../directives';
import { CommonModule } from '@angular/common';
import { OverlayManagerService } from './overlay-panel.service';

@Component({
  selector: 'overlay-panel',
  imports: [ClickOutsideDirective, CommonModule, DropdownsAnimationDirective],
  templateUrl: './overlay-panel.component.html',
  styleUrl: './overlay-panel.component.css',
  animations: [dropdownAnimation], // TODO: remove
})
export class OverlayPanelComponent {
  @Input() overlayClass: string = '';

  @ContentChild('target')
  targetTemplate!: TemplateRef<any>;

  @ContentChild('overlay')
  overlayTemplate!: TemplateRef<any>;

  isOpen: boolean = false;

  @Input() expandSide: 'LEFT' | 'RIGHT' = 'RIGHT';

  constructor(private overlayManager: OverlayManagerService) {}

  ngAfterContentInit() {
    if (!this.targetTemplate) {
      console.error('OverlayPanelComponent: #target template is required');
    }
    if (!this.overlayTemplate) {
      console.error('OverlayPanelComponent: #overlay template is required');
    }
  }

  toggleDropdown(event: Event) {
    // event.stopPropagation();
    // this.isOpen = !this.isOpen;
    event.stopPropagation();
    if (!this.isOpen) {
      this.overlayManager.register(this);
    }
    this.isOpen = !this.isOpen;
    if (!this.isOpen) {
      this.overlayManager.unregister(this);
    }
  }

  closeDropdown() {
    this.isOpen = false;
    this.overlayManager.unregister(this);
  }
}
