import {
  Component,
  computed,
  effect,
  input,
  output,
  signal,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CustomReactiveSearchInputComponent } from '../custom-reactive-search-input/custom-reactive-search-input.component';
import { CustomMultiSelectComponent } from '../custom-multi-select/custom-multi-select.component';
import { IDropdownOption, IUserLookup } from '../../interfaces';
import { debounceTime, Subject } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { CustomDropdownComponent } from '../custom-dropdown/custom-dropdown.component';
import { CustomActionsDropdownComponent } from '../custom-actions-dropdown/custom-actions-dropdown.component';
import { TranslateModule } from '@ngx-translate/core';

export interface FilterChange {
  searchText: string;
  selectedIds: any[];
  selections?: Record<string, any[]>;
}
export interface FilterConfigItem {
  key: string;
  placeholder?: string;
  options: IDropdownOption[];
  selected?: any[];

  height?: string;
  enableFilter?: boolean;
  multiSelect?: boolean;
  showClear?: boolean;
  dropdownContainerClass?: string;
  dropdownOptionsClass?: string;
  isUserMode?: boolean;
  userOptions?: IUserLookup[];
}
interface FilterState {
  searchText: string;
  selectedIdsLegacy: any[];
  selectionsMap: Record<string, any>;
}

@Component({
  selector: 'custom-main-pages-filter',
  imports: [
    FormsModule,
    CustomReactiveSearchInputComponent,
    CustomMultiSelectComponent,
    CustomDropdownComponent,
    CustomActionsDropdownComponent,
    TranslateModule
  ],
  templateUrl: './custom-main-pages-filter.component.html',
  styleUrl: './custom-main-pages-filter.component.css',
})
export class CustomMainPagesFilterComponent {
  // Inputs
  dropdownOptions = input<IDropdownOption[]>([]);
  dropdownSelectedValues = input<any[]>([]);
  dropdownPlaceholder = input<string>('');
  searchInputPlaceholder = input<string>('GENERAL.SEARCH');
  defaultBehaviorFlag = input<boolean>(true);
  configs = input<FilterConfigItem[]>([]);
  moreConfigs = input<FilterConfigItem[]>([]);
  showMore = input<boolean>(false);
  validateNumber = input<boolean>(false);
  externalFiltersHasValue = input<boolean>(false);
  hasFiltered = input<boolean>(false);
  // Output
  filterChange = output<FilterChange>();
  filterReset = output<boolean>();

  // Local state
  searchText = signal<string>('');
  private selectedIdsLegacy: any[] = [];
  selectionsMap = signal<Record<string, any>>({});
  private dropdownChange$ = new Subject<void>();
  private searchChange$ = new Subject<void>();

   initialState = signal<FilterState>({
    searchText: '',
    selectedIdsLegacy: [],
    selectionsMap: {},
  });
hasAnyFilterValue = computed(() => {
    if (this.searchText().trim().length > 0) return true;

    if (this.defaultBehaviorFlag() && this.selectedIdsLegacy.length > 0) {
      return true;
    }

    if (!this.defaultBehaviorFlag()) {
      const selections = this.selectionsMap();
      for (const key in selections) {
        const value = selections[key];
        if (this.hasValue(value)) return true;
      }
    }

    if (this.externalFiltersHasValue()) return true;

    return false;
  });


  hasChangedFromInitial = computed(() => {
    const initial = this.initialState();

    if (this.searchText() !== initial.searchText) return true;

    if (this.defaultBehaviorFlag()) {
      if (!this.arraysEqual(this.selectedIdsLegacy, initial.selectedIdsLegacy)) {
        return true;
      }
    }

    if (!this.defaultBehaviorFlag()) {
      const current = this.selectionsMap();
      const initialSelections = initial.selectionsMap;

      const currentKeys = Object.keys(current);
      const initialKeys = Object.keys(initialSelections);
      if (currentKeys.length !== initialKeys.length) return true;

      for (const key of currentKeys) {
        if (!this.valuesEqual(current[key], initialSelections[key])) {
          return true;
        }
      }
    }

    return false;
  });

  shouldDisableApply = computed(() => {
    if (this.hasAnyFilterValue()) return false;
    return true;
  });

shouldDisableReset = computed(() => {
  return !this.hasFiltered();
});
  constructor() {
    // this.dropdownChange$
    //   .pipe(takeUntilDestroyed(), debounceTime(400))
    //   .subscribe(() => this.emitChange());
    // this.searchChange$
    //   .pipe(takeUntilDestroyed(), debounceTime(300))
    //   .subscribe(() => this.emitChange());
  }
  ngOnInit(): void {
    if (!this.defaultBehaviorFlag()) {
      this.initializeSelectionsFromConfigs();
    }
    this.captureInitialState();
  }
  // Handlers
  onSearch(value: string) {
    this.searchText.set(value ||'');
    this.searchChange$.next();
  }

  onLegacySelectionChange(ids: any[]) {
    this.selectedIdsLegacy = ids ?? [];
    this.dropdownChange$.next();
  }

  onConfigSelectionChange(key: string, ids: any[]) {
    this.setSelectionForKey(key, ids ?? []);
    this.dropdownChange$.next();
  }
  onConfigOneSelectionChange(key: string, obj: any): void {
    const value = obj?.id ?? null;
    this.setSelectionForKey(key, value);
    this.dropdownChange$.next();
  }
  private initializeSelectionsFromConfigs(): void {
    const cfgs = this.configs() ?? [];
    const current = { ...this.selectionsMap() };

    for (const c of cfgs) {
      if (!c?.key) continue;
      if (current[c.key] === undefined) {
        current[c.key] = Array.isArray(c.selected) ? [...c.selected] : [];
      }
    }

    for (const k of Object.keys(current)) {
      if (!cfgs.some((c) => c.key === k)) delete current[k];
    }

    this.selectionsMap.set(current);
  }
  private setSelectionForKey(key: string, value: any): void {
    const next = { ...this.selectionsMap() };
    next[key] = Array.isArray(value) ? [...value] : value;
    this.selectionsMap.set(next);
  }

  private buildLegacyPayload(): FilterChange {
    return {
      searchText: this.searchText(),
      selectedIds: [...this.selectedIdsLegacy],
    };
  }

  private buildConfigPayload(): FilterChange {
    return {
      searchText: this.searchText(),
      selectedIds: this.defaultBehaviorFlag()
        ? [...this.selectedIdsLegacy]
        : [],
      selections: { ...this.selectionsMap() },
    };
  }

  emitChange(): void {
    const payload = this.defaultBehaviorFlag()
      ? this.buildLegacyPayload()
      : this.buildConfigPayload();

    this.filterChange.emit(payload);
    this.captureInitialState();
  }
resetFilters(): void {
  this.searchText.set('');
  this.selectedIdsLegacy = [];

  if (!this.defaultBehaviorFlag()) {
    const resetSelections: Record<string, any> = {};
    const cfgs = this.configs() ?? [];
    for (const c of cfgs) {
      if (c?.key) {
        resetSelections[c.key] = [];
      }
    }
    this.selectionsMap.set(resetSelections);
  }

  //  Reset initial state to empty
  this.initialState.set({
    searchText: '',
    selectedIdsLegacy: [],
    selectionsMap: {},
  });
  this.filterReset.emit(true);
}
  private captureInitialState(): void {
    this.initialState.set({
      searchText: this.searchText(),
      selectedIdsLegacy: [...this.selectedIdsLegacy],
      selectionsMap: this.deepClone(this.selectionsMap()),
    });
  }
  private hasValue(value: any): boolean {
    if (Array.isArray(value)) return value.length > 0;
    return value !== null && value !== undefined;
  }

  private valuesEqual(a: any, b: any): boolean {
    if (Array.isArray(a) && Array.isArray(b)) {
      return this.arraysEqual(a, b);
    }
    return a === b;
  }

  private arraysEqual(a: any[], b: any[]): boolean {
    if (!Array.isArray(a) || !Array.isArray(b)) return a === b;
    if (a.length !== b.length) return false;
    const sortedA = [...a].sort();
    const sortedB = [...b].sort();
    return sortedA.every((val, idx) => val === sortedB[idx]);
  }

  private deepClone(obj: any): any {
    return JSON.parse(JSON.stringify(obj));
  }
  getPlaceholder(c: FilterConfigItem): string {
    return c.placeholder ?? 'Select...';
  }

  /** Normalize UI flags */
  getEnableFilter(c: FilterConfigItem): boolean {
    return c.enableFilter ?? false;
  }

  getShowClear(c: FilterConfigItem): boolean {
    return c.showClear ?? true;
  }

  getHeight(c: FilterConfigItem): string {
    return c.height ?? '4rem';
  }

  getContainerClass(c: FilterConfigItem): string {
    return c.dropdownContainerClass ?? 'filter-dropdown-container';
  }

  getOptionsClass(c: FilterConfigItem): string {
    return c.dropdownOptionsClass ?? 'filter-dropdown-options';
  }

  getSelectedForKey(key: string): any[] {
    return this.selectionsMap()[key] ?? [];
  }
}
