import { Component, input, Input } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { IComponentFormError } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { NgStyle } from '@angular/common';

@Component({
  selector: 'custom-textarea-form',
  standalone: true,
  imports: [CustomAppErrorComponent, ReactiveFormsModule, NgStyle],
  templateUrl: './custom-textarea-form.component.html',
  styleUrl: './custom-textarea-form.component.css',
})
export class CustomTextareaFormComponent {
  @Input() class: string = '';
  @Input() labelClass: string = '';
  @Input() label: string = '';
  @Input() placeholder: string = '';
  resizable = input<'none' | 'both' | 'horizontal' | 'vertical'>('both');
  customHeight = input<string>('');
  @Input({ required: true }) name: string = '';
  @Input({ required: true }) controlName: string = '';
  @Input({ required: true }) parentForm!: FormGroup | any;
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input() rows: number = 3;

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }
}
