import {
  Component,
  ElementRef,
  Input,
  QueryList,
  ViewChildren,
  OnInit,
  OnDestroy,
  AfterViewInit,
} from '@angular/core';
import {
  FormGroup,
  ReactiveFormsModule,
  AbstractControl,
} from '@angular/forms';
import { NgClass, NgStyle } from '@angular/common';
import { Subscription } from 'rxjs';

@Component({
  selector: 'custom-otp-input-form',
  imports: [ReactiveFormsModule, NgStyle],
  templateUrl: './custom-otp-input-form.component.html',
  styleUrl: './custom-otp-input-form.component.css',
})
export class CustomOtpInputFormComponent
  implements OnInit, AfterViewInit, OnDestroy
{
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) controlName!: string;
  @Input() showError: boolean = false;

  @Input() length = 5;
  @ViewChildren('otpBox') boxes!: QueryList<ElementRef<HTMLInputElement>>;

  valueArr: string[] = [];
  private sub?: Subscription;

  // --- lifecycle ---
  ngOnInit(): void {
    this.valueArr = Array(this.length).fill('');
    this.assertControl();
    this.syncBoxesFromControl(this.control.value || '');

    // reflect external writes (e.g., patchValue)
    this.sub = this.control.valueChanges.subscribe((v) =>
      this.syncBoxesFromControl(v ?? '')
    );
  }

  ngAfterViewInit(): void {
    if (!this.control.disabled) {
      setTimeout(() => this.focusBox(0), 500);
    }
  }

  ngOnDestroy(): void {
    this.sub?.unsubscribe();
  }

  // --- convenience ---
  get control(): AbstractControl {
    return this.parentForm.get(this.controlName)!;
  }
  private assertControl(): void {
    const c = this.parentForm.get(this.controlName);
    if (!c)
      throw new Error(
        `[otp-input] control "${this.controlName}" not found in parentForm.`
      );
  }

  // --- DOM helpers ---
  private getBox(i: number): HTMLInputElement | null {
    return this.boxes?.get(i)?.nativeElement ?? null;
  }
  private focusBox(i: number): void {
    const el = this.getBox(i);
    if (el) el.focus();
  }
  private selectBox(i: number): void {
    const el = this.getBox(i);
    if (el) el.select();
  }
  private move(i: number, delta: number): number {
    const next = Math.min(this.length - 1, Math.max(0, i + delta));
    this.focusBox(next);
    return next;
  }

  // --- value helpers ---
  private emitFormValue(): void {
    const val = this.valueArr.join('');
    if (this.control.value !== val)
      this.control.setValue(val, { emitEvent: true });
  }
  private setAt(i: number, ch: string): void {
    this.valueArr[i] = ch;
    this.emitFormValue();
  }
  private clearAt(i: number): void {
    this.valueArr[i] = '';
    this.emitFormValue();
  }
  private syncBoxesFromControl(val: string): void {
    const normalized = (val ?? '').toString().slice(0, this.length);
    this.valueArr = Array(this.length).fill('');
    normalized
      .split('')
      .forEach((ch, i) => (this.valueArr[i] = /\d/.test(ch) ? ch : ''));
    // reflect in DOM if view is ready
    queueMicrotask(() => {
      this.boxes?.forEach(
        (r, i) => (r.nativeElement.value = this.valueArr[i] ?? '')
      );
    });
  }

  // --- sanitation ---
  private onlyDigit(s: string): string {
    const m = s.match(/\d/);
    return m ? m[0] : '';
  }
  private extractDigits(s: string): string[] {
    return (s.match(/\d/g) ?? []).slice(0, this.length);
  }

  // --- paste/multi-fill ---
  private fillFromIndex(start: number, text: string): void {
    const digits = this.extractDigits(text);
    if (!digits.length) return;

    let idx = start;
    for (const d of digits) {
      if (idx >= this.length) break;
      this.valueArr[idx] = d;
      const el = this.getBox(idx);
      if (el) el.value = d;
      idx++;
    }
    this.emitFormValue();

    const nextEmpty = this.valueArr.findIndex((c, j) => j >= start && c === '');
    if (nextEmpty !== -1) {
      this.focusBox(nextEmpty);
      this.selectBox(nextEmpty);
    } else {
      const last = Math.min(this.length - 1, idx);
      this.focusBox(last);
    }
  }

  // --- event handlers ---
  onInput(e: Event, i: number): void {
    const input = e.target as HTMLInputElement;
    const raw = input.value;

    if (raw.length > 1) {
      this.fillFromIndex(i, raw); // handles IME/paste into single box
      return;
    }

    const d = this.onlyDigit(raw);
    input.value = d;
    this.setAt(i, d);

    if (d && i < this.length - 1) {
      this.move(i, +1);
      this.selectBox(i + 1);
    }
  }

  onKeyDown(e: KeyboardEvent, i: number): void {
    const input = e.target as HTMLInputElement;
    switch (e.key) {
      case 'Backspace':
        if (input.value) {
          input.value = '';
          this.clearAt(i);
        } else if (i > 0) {
          const prev = this.move(i, -1);
          const prevEl = this.getBox(prev)!;
          prevEl.value = '';
          this.clearAt(prev);
          this.selectBox(prev);
        }
        e.preventDefault();
        break;

      case 'Delete':
        input.value = '';
        this.clearAt(i);
        e.preventDefault();
        break;

      case 'ArrowLeft':
        this.move(i, -1);
        e.preventDefault();
        break;
      case 'ArrowRight':
        this.move(i, +1);
        e.preventDefault();
        break;

      default:
        if (e.key.length === 1 && !/\d/.test(e.key)) e.preventDefault();
        break;
    }
  }

  onPaste(e: ClipboardEvent, i: number): void {
    const text = e.clipboardData?.getData('text') ?? '';
    if (!text) return;
    e.preventDefault();
    this.fillFromIndex(i, text);
  }
  get indexes(): number[] {
    return Array.from({ length: this.length }, (_, i) => i);
  }
}
