import {
  ApplicationRef,
  ComponentRef,
  EnvironmentInjector,
  Injectable,
  Type,
  createComponent,
  inject,
  InjectionToken,
} from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { CustomModalComponent } from '../../components';
import { TranslateService } from '@ngx-translate/core';

export interface ModalRefApi<T = unknown> {
  close: (result?: T) => void;
}

export const MODAL_REF = new InjectionToken<ModalRefApi>('MODAL_REF');
export function injectModalRef<T = unknown>() {
  return inject(MODAL_REF) as ModalRefApi<T>;
}


export interface ModalOpenOptions<TInputs = unknown> {
  title?: string;
  iconSrc?: string;
  overlayClickClose?: boolean;
  showHeader?: boolean;

}

export interface ModalRef<TChild> {
  modalComponentRef: ComponentRef<CustomModalComponent>;
  childComponentRef: ComponentRef<TChild>;
  close: () => void;
  afterClosed: Promise<void>;
}


@Injectable({ providedIn: 'root' })
export class CustomModalService {
  private readonly applicationRef = inject(ApplicationRef);
  private readonly environmentInjector = inject(EnvironmentInjector);

  /** Open any component inside the modal (child must be standalone or resolvable). */
  constructor(private translate: TranslateService) {}
  async openComponentInModal<TChild, TInputs = unknown>(
    childComponent: Type<TChild>,
    options: ModalOpenOptions<TInputs> = {}
  ): Promise<ModalRef<TChild>> {
    const hostElement = this.createHostElement();
    const modalRef = this.createModal(hostElement);

    this.applyModalInputs(modalRef.instance, options);

    // Render the *ngIf branch so the anchor exists
    modalRef.instance.open();
    modalRef.changeDetectorRef.detectChanges();

    // Wait until the ViewChild anchor is resolved
    await firstValueFrom(modalRef.instance.contentReady$);

    // Attach the child
    const childRef = modalRef.instance.attachContent(childComponent);
    // Resolve when closed, then cleanup
    const afterClosed = this.waitForClose(modalRef).finally(() =>
      this.destroyModal(modalRef, hostElement)
    );

    return {
      modalComponentRef: modalRef,
      childComponentRef: childRef,
      close: () => modalRef.instance.close(),
      afterClosed,
    };
  }

  //  helpers 

  private createHostElement(): HTMLElement {
    const host = document.createElement('div');
    host.classList.add('modal-host'); // styling hook (z-index, fixed pos, etc.)
    document.body.appendChild(host);
    return host;
  }

  private createModal(host: HTMLElement): ComponentRef<CustomModalComponent> {
    const ref = createComponent(CustomModalComponent, {
      environmentInjector: this.environmentInjector,
      hostElement: host,
    });
    this.applicationRef.attachView(ref.hostView);
    return ref;
  }

  private destroyModal(
    ref: ComponentRef<CustomModalComponent>,
    host: HTMLElement
  ): void {
    this.applicationRef.detachView(ref.hostView);
    ref.destroy();
    host.remove();
  }

  private applyModalInputs(modal: CustomModalComponent, o: ModalOpenOptions): void {
     if (o.title) {
    modal.modalTitle = this.translate.instant(o.title);
  }else{
    modal.modalTitle
  }
    modal.modalIcon = o.iconSrc ?? modal.modalIcon;
    modal.showHeader = o.showHeader ?? true;

    modal.overlayClickClose = o.overlayClickClose ?? modal.overlayClickClose;
  }

  private async waitForClose(ref: ComponentRef<CustomModalComponent>): Promise<void> {
    return firstValueFrom(ref.instance.closed);
  }
}
