// activity-time.util.ts

const SECOND = 1_000;
const MINUTE = 60 * SECOND;
const HOUR   = 60 * MINUTE;
const DAY    = 24 * HOUR;
const MONTH  = 30 * DAY;

// hardcoded tiny dict  fast and reliable
const ACTIVITY_DICT = {
  en: {
    ACTIVE_NOW: 'Active now',
    MIN_AGO: (n: number) => `${n} min ago`,
    HR_AGO: (n: number) => `${n} hr ago`,
    DAY_AGO: (n: number) => `${n} day ago`,
  },
  ar: {
    ACTIVE_NOW: ' ',
    MIN_AGO: (n: number) => ` ${n} `,
    HR_AGO: (n: number) => ` ${n} `,
    DAY_AGO: (n: number) => ` ${n} `,
  }
} as const;

function pickLang(lang?: string) {
  return lang && lang.startsWith('ar') ? 'ar' : 'en';
}

export function toDate(input: string | Date | null | undefined): Date | null {
  if (!input) return null;
  if (input instanceof Date) return isNaN(input.getTime()) ? null : input;
  const d = new Date(input);
  return isNaN(d.getTime())
    ? null
    : new Date(d.getTime() - d.getTimezoneOffset() * 60000);
}

// keep your old formatting: "Oct. 18, 2024"
export function formatAbsolute(date: Date, locale = 'en'): string {
  const day = String(date.getDate()).padStart(2, '0');
  const month = new Intl.DateTimeFormat(locale, { month: 'short' }).format(date);
  const formattedMonth = month.endsWith('.') ? month : month + '.';
  const year = date.getFullYear();
  return `${formattedMonth} ${day}, ${year}`;
}

/**
 * show "ago" for < 1 month, otherwise absolute
 */
export function formatActivityTime(
  input: string | Date,
  lang?: string,
  opts?: { now?: Date; activeNowSeconds?: number; locale?: string }
): string {
  const date = toDate(input);
  if (!date) return '-';

  const now = opts?.now ?? new Date();
  const diffMs = now.getTime() - date.getTime();
  const activeNowMs = (opts?.activeNowSeconds ?? 60) * SECOND;

  const picked = pickLang(lang);
  const dict = ACTIVITY_DICT[picked];

  // future  absolute
  if (diffMs < 0) return formatAbsolute(date, opts?.locale ?? picked);

  // active now
  if (diffMs <= activeNowMs) return dict.ACTIVE_NOW;

  // < 1 hour
  if (diffMs < HOUR) {
    const mins = Math.floor(diffMs / MINUTE);
    return dict.MIN_AGO(mins);
  }

  // < 1 day
  if (diffMs < DAY) {
    const hrs = Math.floor(diffMs / HOUR);
    return dict.HR_AGO(hrs);
  }

  // < 1 month
  if (diffMs < MONTH) {
    const days = Math.floor(diffMs / DAY);
    return dict.DAY_AGO(days);
  }

  //  1 month  absolute
  return formatAbsolute(date, opts?.locale ?? picked);
}
