import {
  Component,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
} from '@angular/core';
import {
  GroupedTableConfig,
  TableColumn,
  TableConfig,
} from '../../interfaces/components/dynamic-table.interface';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import {
  actionDeleteSvg,
  actionEditSvg,
  actionViewSvg,
  expandIcon,
  sortSvg,
} from './assets';
import { animate, style, transition, trigger } from '@angular/animations';

@Component({
  selector: 'custom-dynamic-table-with-categories',
  imports: [TranslateModule, NgClass, NgTemplateOutlet],
  templateUrl: './custom-dynamic-table-with-categories.component.html',
    animations: [
    trigger('rowFade', [
      transition(':enter', [
        style({ opacity: 0 }),
        animate('1s ease-in', style({ opacity: 1 })),
      ]),
      transition(':leave', [
        animate('180ms ease-out', style({ opacity: 0 })),
      ]),
    ]),
  ],
  styleUrl: './custom-dynamic-table-with-categories.component.css',
})
export class CustomDynamicTableWithCategoriesComponent {
  @Input() config: GroupedTableConfig | any;
  @Input() hasCheckBox: boolean = true;
  @Input() cellTemplates: { [key: string]: TemplateRef<any> } = {};
  @Input() actionTemplates: { [key: string]: TemplateRef<any> } = {};
  @Output() sortColumn = new EventEmitter<
    string | number | symbol | undefined
  >();
  @Output() nameClick: EventEmitter<any> = new EventEmitter<any>();

  checkedSortIcon: SafeHtml;
  checkedActionViewSvg: SafeHtml;
  checkedActionEditSvg: SafeHtml;
  checkedActionDeleteSvg: SafeHtml;
  expandSvg: SafeHtml;

  constructor(private sanitizer: DomSanitizer) {
    const sortSvgIcon = sortSvg;
    this.checkedSortIcon = this.sanitizer.bypassSecurityTrustHtml(sortSvgIcon);
    const ActionView = actionViewSvg;
    this.checkedActionViewSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionView);
    const ActionEdit = actionEditSvg;
    this.checkedActionEditSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionEdit);
    const ActionDelete = actionDeleteSvg;
    this.checkedActionDeleteSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionDelete);

    const ExpandIcon = expandIcon;
    this.expandSvg = this.sanitizer.bypassSecurityTrustHtml(ExpandIcon);
  }

  onAction(row: any, handler: (row: any) => void): void {
    handler(row);
  }

  getNestedValue(obj: any, path: string): any {
    if (!path) return undefined;
    const parts = path.split(/[\.\[\]']+/).filter(Boolean);
    return parts.reduce((acc, key) => acc?.[key], obj);
  }
}
