import { CommonModule } from '@angular/common';
import {
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  Output,
  ViewChild,
} from '@angular/core';
import { FormControl, FormGroup, ReactiveFormsModule } from '@angular/forms';
import { IComponentFormError, IDropdownOption } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { Subject, takeUntil } from 'rxjs';

@Component({
  selector: 'custom-toggle-switch-form',
  imports: [CommonModule, ReactiveFormsModule],
  templateUrl: './custom-toggle-switch-form.component.html',
  styleUrl: './custom-toggle-switch-form.component.css',
})
export class CustomToggleSwitchFormComponent {
  @Input() label: string = '';
  @Input() size: 'small' | 'medium' | 'large' = 'medium';
  @Input() onColor: string = '#4CAF50';
  @Input() offColor: string = '#757575';
  @Input() thumbColor: string = '#ffffff';
  @Input() disabled: boolean = false;
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) controlName!: string;
  @Input() validation: IComponentFormError[] = [];
  @Input() trueValue!: IDropdownOption;
  @Input() falseValue!: IDropdownOption;
  @Input() height = '3.6em';

  @Output() valueChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  value = false;

  private destroy$ = new Subject<void>();

  ngOnInit() {
    const control = this.parentForm.get(this.controlName);
    if (!control) return;

    // Initialize from current value
    this.value = this.isTrue(control.value);

    // React to external changes
    control.valueChanges.pipe(takeUntil(this.destroy$)).subscribe((val) => {
      this.value = this.isTrue(val);
    });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  toggle() {
    if (this.disabled) return;
    this.value = !this.value;
    this.valueChange.emit(this.value);
    const formValue = this.value
      ? this.trueValue?.id ?? true
      : this.falseValue?.id ?? false;

    // Avoid feedback loop by setting only if different
    const control = this.parentForm.get(this.controlName);
    if (control && control.value !== formValue) {
      control.setValue(formValue);
      control.markAsDirty();
      control.markAsTouched();
    }
  }

  private isTrue(val: any): boolean {
    if (this.trueValue) return val === this.trueValue.id;
    if (this.falseValue) return val !== this.falseValue.id && !!val;
    return !!val;
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }
}
