import { TranslateModule } from '@ngx-translate/core';
import { Component, EventEmitter, input, Input, Output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { checkIcon, deleteIcon, infoSvg } from './assets';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

import { LoadingService } from '../../services';
import { animate, group, keyframes, query, state, style, transition, trigger } from '@angular/animations';
import { CustomTextareaComponent } from '../custom-textarea/custom-textarea.component';
// confirm.types.ts
export type ConfirmType = 'info' | 'delete' |'save';
export type ConfirmResult = 'confirm' | 'cancel' ;

export interface ConfirmOptions {
  message: string;
  modalIcon: string;
  modalTitle: string;
  type: ConfirmType;
  confirmText?: string;
  cancelText?: string;
  extraText?: string;    // optional 3rd button
  successMsg?: string;   // optional success screen
  closeOnBackdrop?: boolean; // default true
  autoCloseMs?: number;  // success screen auto-close (default 1200)
}

const showSuccess = [
  state(
    'void',
    style({
      opacity: 0,
      height: '9.56em',
    })
  ),
  state(
    'visible',
    style({
      opacity: 1,
      height: '26.7em',
    })
  ),
  transition('void => visible', [
    animate(
      '2s ease-in-out',
      keyframes([
        style({ opacity: 0, height: '9.56em', offset: 0 }),
        style({ opacity: 0, height: '7em', offset: 0.5 }),
        style({ opacity: 1, height: '26.7em', offset: 1 }),
      ])
    ),
  ]),
];

const hideConfirm = [
  state('void', style({})),
  state('visible', style({})),
  transition('visible => void', [
    group([
      query(
        '.fade-element',
        [
          animate(
            '1s ease-in-out',
            style({
              opacity: 0,
            })
          ),
        ],
        { optional: true }
      ),
      query(
        '.slide-element',
        [
          animate(
            '2s ease-in-out',
            keyframes([
              style({ opacity: 1, height: '9.56em', offset: 0 }),
              style({ opacity: 0.5, height: '7em', offset: 0.5 }),
              style({ opacity: 0, height: '26.7em', offset: 1 }),
            ])
          ),
        ],
        { optional: true }
      ),
    ]),
  ]),
];


@Component({
  selector: 'custom-confirm-popup',
  imports: [CommonModule, TranslateModule, CustomTextareaComponent],
  templateUrl: './custom-confirm-popup.component.html',
  styleUrl: './custom-confirm-popup.component.css',
  animations: [
    trigger('slideAndFade', hideConfirm),
    trigger('showSuccess', showSuccess),
    // trigger('closePopup', closePopup),
  ],
})
export class CustomConfirmPopupComponent {
  @Input({ required: true }) message: string = '';
  @Input() description: string = '';
  @Input({ required: true }) type: 'info' | 'delete' = 'info';
  @Input() confirmButtonText: string = 'YES';
  @Input() cancelButtonText: string = 'NO';
  @Input() extraButton!: string;
  @Input() extraInput: boolean=false;
  @Output() confirmEvent = new EventEmitter<any>();
  @Output() cancelEvent = new EventEmitter<void>();
  @Output() extraEvent = new EventEmitter<void>();
  @Output() overlayClicked = new EventEmitter<boolean>(false);
  @Output() inputVal = new EventEmitter<string>();
  successMsg = input<string>('');
  checkedInfoSvg: SafeHtml;
  deleteSvg: SafeHtml;
  checkIcon: SafeHtml;
  inputValue='';

  // Animation states
  currentView: 'confirmation' | 'success' = 'confirmation';
  successAnimationState: 'hidden' | 'visible' = 'hidden';
  successPressed = false;

  isVisible = false;
  eventVal: any;

  private _showSuccessScreen = false;

  @Input()
  set showSuccessScreen(value: boolean) {
    this._showSuccessScreen = value;
    if (value) {
      setTimeout(() => {
        this.isVisible = false;
        this.overlayClicked.emit(true);
      }, 6000);
    }
  }

  get showSuccessScreen(): boolean {
    return this._showSuccessScreen;
  }

  constructor(
    private sanitizer: DomSanitizer,
    public loadingService: LoadingService
  ) {
    const infoSvgIcon = infoSvg;
    this.checkedInfoSvg = this.sanitizer.bypassSecurityTrustHtml(infoSvgIcon);
    this.checkIcon = this.sanitizer.bypassSecurityTrustHtml(checkIcon);
    this.deleteSvg = this.sanitizer.bypassSecurityTrustHtml(deleteIcon);
  }

  ngOnInit() {
    this.watchSuccessScreen();
  }

  private watchSuccessScreen() {
    if (this.showSuccessScreen) {
      this.transitionToSuccess();
    }
  }

  ngOnChanges() {
    if (this.showSuccessScreen) {
      this.transitionToSuccess();
    }
  }

  open(event?: any) {
    this.isVisible = true;
    this.eventVal = event;
    this.currentView = 'confirmation';
    this.successAnimationState = 'hidden';
  }

  close() {
    if (!this.showSuccessScreen) {
      this.isVisible = false;
      this.currentView = 'confirmation';
      this.successAnimationState = 'hidden';
    }
  }

  checkSuccess() {
    this.confirmEvent.emit();
    this.successPressed = true;
  }

  private transitionToSuccess() {
    if (this.isVisible) {
      this.currentView = 'success';
      this.successAnimationState = 'visible';
    }
  }

  onOverlayClick(event: MouseEvent) {
    if (event.target === event.currentTarget) {
      this.overlayClicked.emit(true);
      this.isVisible = false;
      this.inputValue='';
    }
  }

  startAnimation(event: any) {
    // console.log('START', event);
  }

  doneAnimation(event: any) {
    // console.log('DONE', event);
  }

  inputValueChanged(event:string){    
    this.inputValue=event;
    this.inputVal.emit(event);
  }
}
