import {
  Directive,
  effect,
  input,
  Input,
  TemplateRef,
  ViewContainerRef,
} from '@angular/core';
import { AuthContextService, AuthService } from '../../services';

@Directive({
  selector: '[canDoAction]',
  standalone: true,
})
export class AuthDirective {
  // private action: string = '';

  canDoAction = input<string[]>([]);

  constructor(
    private authService: AuthService,
    private templateRef: TemplateRef<any>,
    private viewContainer: ViewContainerRef,
    private authContextService: AuthContextService
  ) {
    effect(() => {
      if (
        this.authContextService.userPermissionsAndRoles$() ||
        this.canDoAction().length > 0
      ) {
        const allowed = this.authService.canDoAction(this.canDoAction());

        if (allowed && this.viewContainer.length === 0) {
          this.viewContainer.createEmbeddedView(this.templateRef);
        } else if (!allowed) {
          this.viewContainer.clear();
        }
      }
    });
  }
}
