import { Component, EventEmitter, inject, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ClickOutsideDirective } from '../../directives';
import { IDropdownOption, IUserLookup } from '../../interfaces';
import { NgStyle } from '@angular/common';
import { LocalizePipe } from '../../pipes';
import { TranslationService } from '../../services';
import { TranslateModule } from '@ngx-translate/core';

@Component({
  selector: 'custom-dropdown',
  standalone: true,
  imports: [
    NgStyle,
    FormsModule,
    ClickOutsideDirective,
    LocalizePipe,
    TranslateModule,
  ],
  templateUrl: './custom-dropdown.component.html',
  styleUrls: ['./custom-dropdown.component.css'],
})
export class CustomDropdownComponent {
  @Input() label?: string;
  @Input() labelClass: string = '';
  @Input() dropdownOptionsClass: string = '';
  @Input() dropdownHeaderClass: string = '';
  @Input() selectedClass: string = '';
  @Input() dropdownContainerClass: string = '';
  @Input() placeholder: string = 'GENERAL.SELECT_OPTION';
  @Input() enableFilter: boolean = false;
  @Input() showClear: boolean = true;
  @Input({ required: true }) options: IDropdownOption[] = [];
  @Input({ required: true }) name?: string;
  @Input({ required: true }) value: any;
  @Output() valueChange = new EventEmitter<
    IDropdownOption | IUserLookup | null
  >();
  @Input() height: string = '4.8em';
  @Input() userOptions: IUserLookup[] = [];
  @Input() isUserMode: boolean = false;

  isOpen: boolean = false;
  filteredOptions: IDropdownOption[] = [];
  filterText: string = '';
  filteredUserOptions: IUserLookup[] = [];
  translationService = inject(TranslationService);

  ngOnInit() {
    if (this.isUserMode) {
      this.filteredUserOptions = [...this.userOptions];
    } else {
      this.filteredOptions = [...this.options];
    }
  }

  get selectedOption(): IDropdownOption | null {
    if (this.isUserMode) return null;
    return this.options.find((opt) => opt.id === this.value) || null;
  }
  get selectedUser(): IUserLookup | null {
    if (!this.isUserMode) return null;
    return this.userOptions.find((user) => user.id === this.value) || null;
  }

  toggleDropdown() {
    this.isOpen = !this.isOpen;
    if (this.isOpen) {
      this.filterText = '';
      this.filterOptions();
    }
  }

  selectOption(option: IDropdownOption | IUserLookup) {
    this.value = option.id;
    this.valueChange.emit(option);
    this.isOpen = false;
  }

  clearSelection(event: Event) {
    event.stopPropagation();
    this.value = null;
    this.valueChange.emit(null);
  }

  filterOptions() {
    if (this.isUserMode) {
      if (!this.filterText) {
        this.filteredUserOptions = [...this.userOptions];
      } else {
        const searchText = this.filterText.toLowerCase().trim();
        this.filteredUserOptions = this.userOptions.filter((user) => {
          const firstName = user.firstName.toLowerCase();
          const lastName = user.lastName.toLowerCase();
          const fullName = `${firstName} ${lastName}`;
          const email = user.email.toLowerCase();
          const phone = user.phoneNumber.toLowerCase();

          return (
            firstName.includes(searchText) ||
            lastName.includes(searchText) ||
            fullName.includes(searchText) ||
            email.includes(searchText) ||
            phone.includes(searchText)
          );
        });
      }
    } else {
      if (!this.filterText) {
        this.filteredOptions = [...this.options];
      } else {
        const searchText = this.filterText.toLowerCase();
        this.filteredOptions = this.options.filter((option) =>
          option.nameEn.toLowerCase().includes(searchText)
        );
      }
    }
  }

  @Input() set reset(value: boolean) {
    if (value) {
      this.value = null;
      this.filterText = '';
      this.filterOptions();
    }
  }
}
