import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { formatActivityTime } from '../utilities/activity-time.util';

@Pipe({
  name: 'activityTime',
  standalone: true
})
export class ActivityTimePipe implements PipeTransform {
 constructor(private translate: TranslateService) {}

  transform(
    value: string | Date,
    lang: string = this.translate.currentLang
  ): string {
    // now this will ALWAYS return the right text for the lang
    return formatActivityTime(value, lang, { locale: lang });
  }
}
