import {
  Component,
  ComponentRef,
  EventEmitter,
  HostBinding,
  Injector,
  input,
  Input,
  Output,
  Provider,
  signal,
  Type,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReplaySubject } from 'rxjs';
import { MODAL_REF, ModalRefApi } from '../../services';
import { DropdownsAnimationDirective } from '../../directives';
import { trigger, transition, style, animate } from '@angular/animations';

const expandingAnimation = trigger('dropdown', [
  transition('void => enter', [
    style({ height: '0', opacity: 0, overflow: 'hidden' }),
    animate('500ms ease-out', style({ height: '100%', opacity: 1 })),
  ]),
  transition('enter => void', [
    style({
      height: '100%',
      maxHeight: '100%',
      opacity: 1,
      overflow: 'hidden',
    }),
    animate(
      '500ms ease-in',
      style({ height: '0', maxHeight: '0', opacity: 0 })
    ),
  ]),
]);

const expandingOuterAnimation = trigger('expandingAnimation', [
  transition('leave => enter', [
    style({ height: '0' }),
    animate('200ms ease-out', style({ height: '*' })),
  ]),
  transition('enter => leave', [
    style({ height: '*' }),
    animate('200ms ease-in', style({ height: '0' })),
  ]),
]);

@Component({
  selector: 'custom-expanding-container',
  standalone: true,
  imports: [DropdownsAnimationDirective],
  templateUrl: './custom-expanding-container.component.html',
  styleUrls: ['./custom-expanding-container.component.css'],
  animations: [expandingAnimation, expandingOuterAnimation],
})
export class CustomExpandingContainerComponent {
  @Input() expanded = false;
  @Input() showIcon1 = true;
  @Input() padding = '2.3em 2.4em';
  @Input() borderRadius = '1em';
  @Input() backgroundColor = '#ffffff';
  @Input() showBorderBottom = true;
  @Input() minMaxHeight = '8em';
  @Input() minExpandingHeight = '20em'; //'36em';

  @Output() toggled: EventEmitter<boolean> = new EventEmitter<boolean>();

  isExpanded = signal(false);

  @HostBinding('style.--min-max-height') minMaxHeightCss!: string;
  @HostBinding('style.--min-expanded-height') minExpandedHeightCss!: string;
  @HostBinding('class.isExpanded') get isExpandedClass() {
    return this.isExpanded();
  }

  ngOnInit() {
    this.isExpanded.set(this.expanded);
    this.minMaxHeightCss = this.minMaxHeight;
    this.minExpandedHeightCss = this.minExpandingHeight;
  }

  toggle(): void {
    this.isExpanded.update((value) => !value);
    this.toggled.emit(this.isExpanded());
  }

  open(): void {
    this.isExpanded.set(true);
  }

  close(): void {
    this.isExpanded.set(false);
  }
}
