import {
  HttpHandlerFn,
  HttpInterceptorFn,
  HttpRequest,
  HttpResponse,
} from '@angular/common/http';
import { AuthConstant } from '../../enums/auth/auth.constant';
import { I18nConstant } from '../../enums/i18n/i18n.constant';
import { SHOW_SUCCESS_TOASTER, SKIP_TOKEN } from '../../utilities/http-context';
import { inject, PLATFORM_ID } from '@angular/core';
import { ToastService } from '../../services';
import { map } from 'rxjs';
import { isPlatformBrowser } from '@angular/common';

export const AuthInterceptor: HttpInterceptorFn = (
  request: HttpRequest<unknown>,
  next: HttpHandlerFn
) => {
  const token = localStorage.getItem(AuthConstant.TOKEN);
  const translate = localStorage.getItem(I18nConstant.LANGUAGE);
  const skipToken = request.context.get(SKIP_TOKEN);
  const showSuccessToaster = request.context.get(SHOW_SUCCESS_TOASTER);

  const toastService = inject(ToastService);

  // 'Content-Type': 'application/json',
  const headersConfig = {
    'accept-language': translate === I18nConstant.EN ? 'en-US' : 'e.g',
  } as { [name: string]: string | string[] };

  if (!skipToken) {
    headersConfig['Authorization'] = `Bearer ${token}`;
  }

  const clonedRequest = request.clone({
    setHeaders: headersConfig,
  });

  return next(clonedRequest).pipe(
    map((event) => {
      //&& isPlatformBrowser(PLATFORM_ID)
      if (event instanceof HttpResponse ) {

        const body = event.body as {
          status: string;
          message: string;
          success: boolean;
          statusCode: number;
          errors: { msg: string }[];
        };
        if (body.status === 'SUCCESS' || body.success) {
          body['success'] = true;
        } else {
          body['success'] = false;
        }
        //  body['statusCode'] = event.status;

       // console.log('body: ', body['success'] );

    //    console.log('request.method: ', request.method);
        if (
          body &&
          body.success &&
          (request.method === 'POST' ||
            request.method === 'PUT' ||
            request.method === 'DELETE')
        ) {
          // this works for registeration app
          // if (showSuccessToaster && body.message ) {
          //   toastService.toast(
          //     `${body.message}`,
          //     'top-center',
          //     'success',
          //     4000
          //   );
          // }

          // if (!body.success) {
          //   if (body.errors && body.errors.length > 0) {
          //     body.errors.forEach((error) => {
          //       toastService.toast(`${error.msg}`, 'top-center', 'error', 2000);
          //     });
          //   } else {
          //     toastService.toast(`Unknown Error`, 'top-center', 'error', 2000);
          //   }
          // }
        }
      }
      return event;
    })
  );
};
