import { NgClass, NgStyle } from '@angular/common';
import { Component, Input } from '@angular/core';

@Component({
  selector: 'custom-progress-bar',
  imports: [NgStyle, NgClass],
  templateUrl: './custom-progress-bar.component.html',
  styleUrl: './custom-progress-bar.component.css',
})
export class CustomProgressBarComponent {
  @Input() value = 0;
  @Input() color = '#D9FFF7';
  @Input() colorGradient!: string;
  @Input() emptyColor = '#D9FFF7';
  @Input() showProgressInline = true;
  @Input() barHeight = '5px';

  // var(--bar-height, 5)
  // [ngStyle]="{ '--bar-height': gridCols }">
}
