import { Injectable, signal } from '@angular/core';
import { StorageService } from './storage.services';
import {
  ILocalData,
  ILoginData,
  ISessionData,
  IUserData,
} from '../../interfaces';
import { BehaviorSubject } from 'rxjs';
import { AuthConstant } from '../../enums';
import { ActivatedRouteSnapshot } from '@angular/router';

@Injectable({
  providedIn: 'root',
})
export class AuthContextService {
  userLocalData$ = signal<ILocalData | null>(null);
  userPermissionsAndRoles$ = signal<ISessionData | null>(null);
  constructor(private storageService: StorageService) {}
  saveTokens(data: ILoginData) {
    let localData: ILocalData = {
      accessToken: data.accessToken,
      refreshToken: data.refreshToken,
      user: data.user,
    };
    this.userLocalData$.set(localData);
    this.storageService.setLocalStorage(localData);
  }

  savePermissionsAndRoles(data: ISessionData) {
    let permissions: ISessionData = {
      roles: data.roles,
      permissions: data.permissions,
      rolesDetails:data.rolesDetails
    };
   this.userPermissionsAndRoles$.set(permissions);
    this.storageService.setSession(permissions);
  }

  clearData() {
    this.userLocalData$.set(null);
    this.userPermissionsAndRoles$.set(null);
    this.storageService.clearAll();
  }
}
