import { Component, effect, EventEmitter, Input, input, Output, signal } from '@angular/core';
import { CustomTabsComponent } from '../custom-tabs/custom-tabs.component';
import { ITabDropdownOption } from '../../interfaces';
import { TranslateModule } from '@ngx-translate/core';
import { PERMISSIONS } from '../../enums';
import { AuthService } from '../../services';

@Component({
  selector: 'custom-pages-header',
  imports: [CustomTabsComponent, TranslateModule],
  templateUrl: './custom-pages-header.component.html',
  styleUrl: './custom-pages-header.component.css',
})
export class CustomPagesHeaderComponent {
  headerTitle = input<string>('');
  addPermission =input<PERMISSIONS>(PERMISSIONS.all);
  btnTitle = input<string>('');
  hasTabs = input<boolean>(false);
  hideBtn = input<boolean>(false);
  listCounter = input<number>(0);

  //@Input() hasTabs:any
  pageTabs = input<ITabDropdownOption[]>([]);
  selectedTab = signal<ITabDropdownOption>({} as ITabDropdownOption );
  selectedTabInput = input<ITabDropdownOption>({} as ITabDropdownOption );

  @Output() tabSelected: EventEmitter<ITabDropdownOption> =
    new EventEmitter<ITabDropdownOption>();
  @Output() addAction: EventEmitter<any> = new EventEmitter<any>();
  constructor(public authService :AuthService){
    effect(()=>{
      if(this.pageTabs().length){
        console.log('tabs' , this.pageTabs())
          this.selectedTab.set(this.pageTabs()[0])
      }
    })
  }


  selectTab(tab: ITabDropdownOption) {
    if (!tab.disabled) {
      this.selectedTab.set(tab)
      this.tabSelected.emit(tab);
    }
  }
  onAddClick() {
    this.addAction.emit();
  }
}
