import { IDropdownOption } from '../../interfaces/components/dropdown.interface';
import {
  Component,
  EventEmitter,
  HostListener,
  input,
  Input,
  Output,
} from '@angular/core';
import { CustomBreadcrumbComponent } from '../custom-breadcrumb/custom-breadcrumb.component';
import { detailDD, xMark } from './assets';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { OverlayPanelComponent } from '../overlay-panel/overlay-panel.component';

export interface IActionsList extends IDropdownOption {
  icon?: string;
}

@Component({
  selector: 'custom-details-header',
  imports: [CustomBreadcrumbComponent, OverlayPanelComponent],
  templateUrl: './custom-details-header.component.html',
  styleUrl: './custom-details-header.component.css',
})
export class CustomDetailsHeaderComponent {
  @Input() showX: boolean = true;
  @Input({ required: true }) BreadCrumbs!: { label: string; url: string }[];
  @Output() closeContainer: EventEmitter<void> = new EventEmitter<void>();
  @Output() dropdownSelectAction: EventEmitter<string | number> =
    new EventEmitter<string | number>();
  xMarkSvg!: SafeHtml;
  DetailDDSvg!: SafeHtml;
  showDropdown: boolean = false;

  breadCrumb: { label: string; url: string }[] = [
    {
      label: 'Home',
      url: `/#`,
    },
    {
      label: 'Task',
      url: `/#`,
    },
    {
      label: 'View Task',
      url: `/#`,
    },
  ];

  actionsDropdown = input<IActionsList[]>([]);

  constructor(private sanitizer: DomSanitizer) {
    this.xMarkSvg = this.sanitizer.bypassSecurityTrustHtml(xMark);
    this.DetailDDSvg = this.sanitizer.bypassSecurityTrustHtml(detailDD);
  }

  sanitizeSvg(svg: string): SafeHtml {
    return this.sanitizer.bypassSecurityTrustHtml(svg);
  }
  toggleDropdown(event: MouseEvent) {
    event.stopPropagation();
    this.showDropdown = !this.showDropdown;
  }
}
