import { NgStyle } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';


@Component({
  selector: 'custom-color',
 imports: [NgStyle, FormsModule],
  templateUrl: './custom-color.component.html',
  styleUrl: './custom-color.component.css'
})
export class CustomColorComponent  {
  colorsArray: string[] = [
    '#9747FF',
    '#AC8746',
    '#8290E1',
    '#E76161',
    '#4F378B',
    '#FFD8E4',
    '#1D1B20',
    '#852221',
    '#FF8981',
    '#9887C5',
    '#3B383E',
    '#46115E',
    '#F57423',
    '#FFCB1F',
  ];

  isOpen: boolean = false;
  @Input({ required: true }) positionClass: string = '';
  @Input({ required: true }) title: string = '';
  @Input() name: string = '';
  @Input() hideNameInput: boolean = false;
  @Input() color: string = '#9747FF';
  @Input() enableColors: boolean = true;
  @Output() onSaveColor: EventEmitter<{
    color?: string;
    name?: string;
  }> = new EventEmitter<{
    color?: string;
    name?: string;
  }>();

  onColorSelected(color: string) {
    if (this.color === color) {
      this.color = '';
      return;
    }
    this.color = color;
  }

  onCancel() {
    this.isOpen = false;
  }

  onSave() {
    this.onSaveColor.emit({
      color: this.color,
      name: this.name,
    });
    this.name = '';
    this.color = '';
    this.isOpen = false;
  }
}
