import { CommonModule } from '@angular/common';
import { Component, EventEmitter, input, Input, Output } from '@angular/core';
import { CustomDetailsHeaderComponent } from '../custom-details-header/custom-details-header.component';
import { IDropdownOption } from '../../interfaces';

@Component({
  selector: 'custom-details-modal',
  imports: [CommonModule, CustomDetailsHeaderComponent],
  templateUrl: './custom-details-modal.component.html',
  styleUrl: './custom-details-modal.component.css',
})
export class CustomDetailsModalComponent {
  @Input() modalTitle = '';
  @Input() overlayClickClose: boolean = true;
  @Input({ required: true }) BreadCrumbs!: { label: string; url: string }[];
  @Output() headerButtonClick = new EventEmitter<void>();
  @Input() sectionDimensions: string = '10fr 5fr';
  actionsDropdown = input<IDropdownOption[]>([]);
  @Output() dropdownSelectAction: EventEmitter<string | number> =
    new EventEmitter<string | number>();
  isVisible = true;
  @Input() mainSize = 0;
  @Input() sideSize = 0;

  ngOnInit() {
    if (
      this.sectionDimensions === '10fr 5fr' &&
      this.mainSize !== 0 &&
      this.sideSize !== 0
    ) {
      this.sectionDimensions = `${this.mainSize}fr ${this.sideSize}fr`;
    }
  }
  open() {
    this.isVisible = true;
  }

  close() {
    this.isVisible = false;
    this.headerButtonClick.emit();
  }

  onOverlayClick(event: MouseEvent) {
    if (event.target === event.currentTarget && this.overlayClickClose) {
      this.close();
    }
  }
}
