import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ClickOutsideDirective } from '../../directives';
import { CommonModule } from '@angular/common';

export type CalendarValue = Date | null;
export interface CalendarRange {
  startDate: Date | null;
  endDate: Date | null;
}

@Component({
  selector: 'custom-calendar',
  standalone: true,
  imports: [FormsModule, ClickOutsideDirective, CommonModule],
  templateUrl: './custom-calendar.component.html',
  styleUrls: ['./custom-calendar.component.css'],
})
export class CustomCalendarComponent {
  @Input() label: string = '';
  @Input() placeholder: string = 'Select date';
  @Input() labelClass: string = '';
  @Input() calendarPopUpClass: string = '';
  @Input() calendarInputClass: string = '';
  @Input() calendarContainerClass: string = '';
  @Input() minDate: Date | null = null;
  @Input() maxDate: Date | null = null;
  @Input() height: string = '4.8em';

  @Input() value: CalendarValue = null;
  @Input() rangeMode: boolean = false;
  @Input() showCalendar: boolean = false;
  @Input() startDate: Date | null = null;
  @Input() endDate: Date | null = null;

  @Output() valueChange = new EventEmitter<CalendarValue>();
  @Output() rangeChange = new EventEmitter<CalendarRange>();

  isFocused = false;
  viewMode: 'days' | 'months' | 'years' = 'days';

  currentMonth: Date = new Date();
  days: Date[] = [];
  weekdays = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

  months = [
    'January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'
  ];

  years: number[] = [];

  constructor() {
    this.generateCalendar();
    this.generateYears();
  }

  toggleCalendar(): void {
    this.showCalendar = !this.showCalendar;
  }

  selectDate(date: Date): void {
    if (this.rangeMode) {
      this.handleRangeSelection(date);
      return;
    }

    this.value = date;
    this.valueChange.emit(date);
  }

  handleRangeSelection(date: Date): void {
    if (!this.startDate) {
      this.startDate = date;
    } else if (!this.endDate) {
      this.endDate = date >= this.startDate ? date : null;
      if (!this.endDate) this.startDate = date;
    } else {
      this.startDate = date;
      this.endDate = null;
    }

    this.rangeChange.emit({ startDate: this.startDate, endDate: this.endDate });

    if (!this.rangeMode) {
      this.showCalendar = false;
    }
  }

  generateCalendar(): void {
    this.days = [];
    const year = this.currentMonth.getFullYear();
    const month = this.currentMonth.getMonth();

    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);

    for (let i = firstDay.getDay() - 1; i >= 0; i--) {
      this.days.push(new Date(year, month, -i));
    }

    for (let i = 1; i <= lastDay.getDate(); i++) {
      this.days.push(new Date(year, month, i));
    }

    for (let i = 1; i <= 6 - lastDay.getDay(); i++) {
      this.days.push(new Date(year, month + 1, i));
    }
  }

  isSelected(date: Date): boolean | null {
    if (this.rangeMode) {
      return (
        (this.startDate && date.toDateString() === this.startDate.toDateString()) ||
        (this.endDate && date.toDateString() === this.endDate.toDateString())
      );
    }

    return this.value ? date.toDateString() === this.value.toDateString() : false;
  }

  isInRange(date: Date): boolean {
    if (!this.rangeMode || !this.startDate || !this.endDate) return false;
    return date > this.startDate && date < this.endDate;
  }

  isCurrentMonth(date: Date): boolean {
    return date.getMonth() === this.currentMonth.getMonth();
  }

  isDisabled(date: Date): boolean | null{
    return (
      (this.minDate && date < this.minDate) ||
      (this.maxDate && date > this.maxDate)
    );
  }

  getMonthName(): string {
    return this.currentMonth.toLocaleString('default', { month: 'long' });
  }

  getYear(): number {
    return this.currentMonth.getFullYear();
  }

  formatDisplayDate(): string {
    if (this.rangeMode) {
      if (!this.startDate) return '';
      if (!this.endDate) return `${this.startDate.toLocaleDateString('en-GB')} - ...`;
      return `${this.startDate.toLocaleDateString('en-GB')} - ${this.endDate.toLocaleDateString('en-GB')}`;
    }

    return this.value ? this.value.toLocaleDateString('en-GB') : '';
  }

  clearSelection(event: Event): void {
    event.stopPropagation();

    if (this.rangeMode) {
      this.startDate = null;
      this.endDate = null;
      this.rangeChange.emit({ startDate: null, endDate: null });
    } else {
      this.value = null;
      this.valueChange.emit(null);
    }
  }

  generateYears(): void {
    const currentYear = new Date().getFullYear();
    for (let i = currentYear - 50; i <= currentYear + 50; i++) {
      this.years.push(i);
    }
  }

  openMonthPicker(event: MouseEvent): void {
    event.stopPropagation();
    this.viewMode = 'months';
  }

  openYearPicker(event: MouseEvent): void {
    event.stopPropagation();
    this.viewMode = 'years';
  }

  selectMonth(index: number, event: MouseEvent): void {
    event.stopPropagation();
    this.currentMonth = new Date(this.currentMonth.getFullYear(), index, 1);
    this.viewMode = 'days';
    this.generateCalendar();
  }

  selectYear(year: number, event: MouseEvent): void {
    event.stopPropagation();
    this.currentMonth = new Date(year, this.currentMonth.getMonth(), 1);
    this.viewMode = 'months';
  }

  prevMonth(event: MouseEvent): void {
    event.stopPropagation();
    this.currentMonth = new Date(this.currentMonth.getFullYear(), this.currentMonth.getMonth() - 1, 1);
    this.generateCalendar();
  }

  nextMonth(event: MouseEvent): void {
    event.stopPropagation();
    this.currentMonth = new Date(this.currentMonth.getFullYear(), this.currentMonth.getMonth() + 1, 1);
    this.generateCalendar();
  }

  isSelectedMonth(index: number): boolean {
    return this.currentMonth.getMonth() === index;
  }

  isSelectedYear(year: number): boolean {
    return this.currentMonth.getFullYear() === year;
  }
}
