import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'custom-search-input',
  imports: [FormsModule],
  templateUrl: './custom-search-input.component.html',
  styleUrl: './custom-search-input.component.css',
})
export class CustomSearchInputComponent {
  @Input() class: string = '';
  @Input() labelClass: string = '';
  @Input() label: string = '';
  @Input() placeholder: string = '';
  @Input() name: string = '';
  @Input({ required: true }) value: any;
  @Input() style: 'base' | 'white' | 'grey' = 'base'; // ps. these have effect on style and color
  @Output() valueChange = new EventEmitter<any>();
}
