import {
  Component,
  EventEmitter,
  Input,
  Output,
  SimpleChanges,
  ViewChild,
  ElementRef,
  inject,
} from '@angular/core';
import {
  FormGroup,
  FormsModule,
  ReactiveFormsModule,
  AbstractControl,
  Validators,
} from '@angular/forms';
import { NgStyle } from '@angular/common';
import { ClickOutsideDirective } from '../../directives';
import { IComponentFormError, IDropdownOption } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { LocalizePipe } from '../../pipes';
import { TranslationService } from '../../services';
import parsePhoneNumber from 'libphonenumber-js/max'; // or 'libphonenumber-js' (min) [web:2]

export interface IPhoneCountryOption extends IDropdownOption {
  dialCode: string; // "+966"
  iso2: string; // "SA"  <-- add this
  countryIcon?: string;
  patternMessage?: string;
  pattern?: RegExp;
}

export interface IPhoneValue {
  countryCode: string;
  number: string;
}
export type PhoneRule = { pattern: RegExp; message?: string };

export function toE164OrNull(
  dialCode: string,
  nationalDigits: string
): string | null {
  const cc = (dialCode || '').replace('+', '');
  const raw = `+${cc}${nationalDigits}`;
  const pn = parsePhoneNumber(raw);
  if (!pn) return null;
  return pn.isValid() ? pn.number : null; // pn.number is E.164. [web:2]
}

@Component({
  selector: 'custom-phone-form',
  standalone: true,
  imports: [
    NgStyle,
    FormsModule,
    ReactiveFormsModule,
    ClickOutsideDirective,
    CustomAppErrorComponent,
    LocalizePipe,
  ],
  templateUrl: './custom-phone-form.component.html',
  styleUrls: ['./custom-phone-form.component.css'],
})
export class CustomPhoneFormComponent {
  /* ------------ Inputs / Outputs ------------ */
  @Input({ required: true }) parentForm!: FormGroup | any;
  @Input({ required: true }) controlName!: string;
  @Input({ required: true }) name!: string;

  @Input() label?: string;
  @Input() labelClass: string = '';

  // Container & inner parts classes for styling parity with your dropdown component
  @Input() containerClass: string = ''; // applied on .phone-container
  @Input() countryHeaderClass: string = ''; // applied on .phone-country
  @Input() optionsPanelClass: string = ''; // applied on .phone-options
  @Input() numberInputClass: string = ''; // applied on .phone-input

  @Input() placeholderNumber: string = 'Enter phone number';
  @Input() enableFilter: boolean = false;

  /** Country list is required. Each option should provide id, nameEn, dialCode, and (optionally) rules. */
  @Input({ required: true }) countries: IPhoneCountryOption[] = [];

  /** Your existing error system (same as dropdown) */
  @Input({ required: true }) validation: IComponentFormError[] = [];

  @Input() disabled: boolean = false;
  @Input() height: string = '4.8em';

  /** Optional country-specific rules override: { '+966': {min:9,max:9,pattern:/^5\d{8}$/} } */
  @Input() phoneRules?: Record<string, { pattern?: RegExp; message?: string }>;

  @Output() valueChange = new EventEmitter<IPhoneValue | null>();

  /* ------------ Local state ------------ */
  isOpen: boolean = false;
  filterText: string = '';
  filteredCountries: IPhoneCountryOption[] = [];
  selectedCountry: IPhoneCountryOption | null = null;
  phoneText: string = '';

  @ViewChild('numberInputRef') numberInputRef!: ElementRef<HTMLInputElement>;

  /* ------------ Lifecycle ------------ */
  translationService = inject(TranslationService);
  ngOnInit() {
    this.filteredCountries = [...this.countries];
    this.selectCountry(this.filteredCountries[0]);
    this.setupFormControlBinding();
    if (this.disabled) this.parentControl()?.disable({ emitEvent: false });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['countries'] && this.countries?.length) {
      this.filteredCountries = [...this.countries];
      this.syncFromControl();
    }
  }

  /* ------------ Core bindings ------------ */
  private parentControl(): AbstractControl | null {
    return this.parentForm?.get?.(this.controlName) ?? null;
  }

  private setupFormControlBinding(): void {
    const ctrl = this.parentControl();
    if (!ctrl) return;

    // Initial load from control
    this.syncFromControl();

    // Reflect future external changes
    ctrl.valueChanges.subscribe(() => this.syncFromControl());
  }

  /** Pull current control value and mirror into local UI state */
  private syncFromControl(): void {
    const ctrl = this.parentControl();
    if (!ctrl) return;

    const val = (ctrl.value || {}) as Partial<IPhoneValue>;
    const dial = (val.countryCode ?? '').trim();
    const num = (val.number ?? '').toString();

    // Update selected country from dial code
    this.selectedCountry = this.findCountryByDial(dial);
    this.phoneText = num;
  }

  /* ------------ Dropdown + filter ------------ */
  toggleDropdown(): void {
    if (this.disabled) return;
    this.isOpen = !this.isOpen;
    if (this.isOpen) {
      this.filterText = '';
      this.applyFilter();
    } else {
      this.markTouched();
    }
  }

  closeDropdown(): void {
    this.isOpen = false;
    this.markTouched();
  }

  applyFilter(): void {
    if (!this.filterText) {
      this.filteredCountries = [...this.countries];
      return;
    }
    const q = this.filterText.toLowerCase();
    this.filteredCountries = this.countries.filter(
      (c) =>
        c.nameEn.toLowerCase().includes(q) ||
        c.dialCode.toLowerCase().includes(q)
    );
  }

  selectCountry(option: IPhoneCountryOption): void {
    this.selectedCountry = option;
    this.isOpen = false;
    this.pushToForm();
    //  this.markTouched();
    //    queueMicrotask(() => this.numberInputRef?.nativeElement?.focus());
  }

  clearCountry(e: Event): void {
    e.stopPropagation();
    this.selectedCountry = null;
    this.pushToForm();
  }

  /* ------------ Number input ------------ */
  onNumberKeydown(e: KeyboardEvent): void {
    if (this.isSystemKey(e)) return;
    if (!/^\d$/.test(e.key)) e.preventDefault();
  }

  private isSystemKey(e: KeyboardEvent): boolean {
    const nav = [
      'Backspace',
      'Delete',
      'Tab',
      'Escape',
      'Enter',
      'ArrowLeft',
      'ArrowRight',
      'Home',
      'End',
    ];
    return nav.includes(e.key) || e.ctrlKey || e.metaKey;
  }

  // Clean paste: only digits; if non-digits found, prevent default and insert sanitized
  onNumberPaste(e: ClipboardEvent): void {
    const text = e.clipboardData?.getData('text') ?? '';
    if (/^\d+$/.test(text)) return; // ok
    e.preventDefault();
    this.insertAtCursor(this.onlyDigits(text));
  }

  // Helper to insert text at the current caret position
  private insertAtCursor(text: string): void {
    const input = this.numberInputRef?.nativeElement;
    if (!input) return;
    const start = input.selectionStart ?? input.value.length;
    const end = input.selectionEnd ?? input.value.length;

    const next = input.value.slice(0, start) + text + input.value.slice(end);
    this.phoneText = this.onlyDigits(next);
    this.pushToForm();

    queueMicrotask(() => {
      const pos = start + text.length;
      input.setSelectionRange(pos, pos);
    });
  }

  onNumberInput(raw: string): void {
    const digits = this.onlyDigits(raw);
    if (digits !== raw) {
      this.phoneText = digits; // immediately stop writing non-digits
      this.pushToForm();
      // keep UX smooth: move caret to end (simple & safe)
      queueMicrotask(() => {
        const el = this.numberInputRef?.nativeElement;
        if (el) {
          const pos = digits.length;
          el.setSelectionRange(pos, pos);
        }
      });
      return;
    }
    // normal path
    this.phoneText = digits;
    this.pushToForm();
  }
  onNumberBlur(): void {
    this.normalizePhoneText();
    this.pushToForm();
    this.markTouched();
  }

  private normalizePhoneText(): void {
    const dial = this.selectedCountry?.dialCode || '';
    const digits = this.getDigits(this.phoneText);

    const full = `+${dial.replace('+', '')}${digits}`;
    const pn = parsePhoneNumber(full);

    if (pn && pn.isValid()) {
      // Store national significant number (digits without country calling code).
      this.phoneText = pn.nationalNumber; // [web:2]
    }
  }

  private onlyDigits(text: string): string {
    return (text || '').replace(/\D+/g, '');
  }

  /* ------------ Form sync + validation ------------ */
  private pushToForm(): void {
    const ctrl = this.parentControl();
    if (!ctrl) return;

    const value: IPhoneValue | null = this.composeValue();
    ctrl.setValue(value, { emitEvent: true });
    this.applyValidation(ctrl, value);
    this.valueChange.emit(value);
  }

  private composeValue(): IPhoneValue | null {
    const code = this.selectedCountry?.dialCode ?? '';
    const num = this.phoneText ?? '';
    if (!code && !num) return null; // keep pristine null if empty
    return { countryCode: code, number: num };
  }

  private applyValidation(
    ctrl: AbstractControl,
    value: IPhoneValue | null
  ): void {
    const isRequired = this.containRequiredError();
    const errors: Record<string, any> = {};

    const dial = value?.countryCode?.trim() || '';
    const digits = this.getDigits(value?.number || '');

    if (!dial || !digits) {
      if (isRequired) errors['required'] = true;
      ctrl.setErrors(Object.keys(errors).length ? errors : null);
      return;
    }

    // Build full international number from the selected dial code + entered digits.
    const full = `+${dial.replace('+', '')}${digits}`;
    const phoneNumber = parsePhoneNumber(full);

    if (!phoneNumber) {
      errors['phone'] = { message: 'Not a phone number' };
    } else if (!phoneNumber.isValid()) {
      errors['phone'] = {
        message: 'Invalid phone number for selected country',
      };
    }

    ctrl.setErrors(Object.keys(errors).length ? errors : null);
  }

  private resolveRules(dialCode: string): PhoneRule {
    const fromInput = this.phoneRules ? this.phoneRules[dialCode] : undefined;
    if (fromInput && fromInput.pattern) {
      return { pattern: fromInput.pattern, message: fromInput.message };
    }
    const opt = this.findCountryByDial(dialCode);
    if (opt && opt.pattern) {
      return { pattern: opt.pattern, message: opt.patternMessage };
    }
    return { pattern: /^\d{6,15}$/, message: 'Invalid phone format' };
  }

  private buildDefaultPatternMessage(dialCode: string): string {
    const country = this.findCountryByDial(dialCode)?.nameEn || 'this country';
    return `Invalid phone number for ${country}`;
  }

  private buildPatternMessage(dialCode: string, rule: PhoneRule): string {
    return rule.message || this.buildDefaultPatternMessage(dialCode);
  }

  private getDigits(text: string): string {
    return (text || '').replace(/\D+/g, '');
  }

  private markTouched(): void {
    this.parentControl()?.markAsTouched();
  }

  private findCountryByDial(dial: string): IPhoneCountryOption | null {
    if (!dial) return null;
    return this.countries.find((c) => c.dialCode === dial) ?? null;
  }

  getMaxLength(): number | null {
    const control = this.parentForm.controls[this.controlName];

    if (!control || !control.validator) {
      return null;
    }

    // Trigger maxLength error with an infinitely long value
    const validatorResult = control.validator({
      value: { length: Infinity },
    } as any);

    // If maxLength validator exists, it will return an error with requiredLength
    if (validatorResult && validatorResult['maxlength']) {
      return validatorResult['maxlength'].requiredLength;
    }

    return null;
  }

  getMinLength(): number | null {
    const control = this.parentForm.controls[this.controlName];

    if (!control || !control.validator) {
      return null;
    }

    // Trigger minLength error with an infinitely long value
    const validatorResult = control.validator({
      value: { length: Infinity },
    } as any);

    // If minLength validator exists, it will return an error with requiredLength
    if (validatorResult && validatorResult['minlength']) {
      return validatorResult['minlength'].requiredLength;
    }

    return null;
  }

  /* ------------ Shared with your dropdown pattern ------------ */
  containRequiredError(): boolean {
    return this.validation?.some((v) =>
      v.errorType?.includes?.(ComponentFormErrorConstant.REQUIRED)
    );
  }
}
