import { Component, EventEmitter, Input, Output, TemplateRef, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { FormGroup, AbstractControl, ReactiveFormsModule } from '@angular/forms';
import { NgClass, NgIf, NgTemplateOutlet } from '@angular/common';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { IComponentFormError } from '../../interfaces';
@Component({
  selector: 'custom-counter-input',
  imports: [ReactiveFormsModule, NgTemplateOutlet, CustomAppErrorComponent],
  templateUrl: './custom-counter-input.component.html',
  styleUrl: './custom-counter-input.component.css'
})
export class CustomCounterInputComponent implements OnInit, OnChanges {
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) controlName!: string;

  @Input() step = 1;
  @Input() min = 0;                  // not less than 0 (default)
  @Input() max: number | null = null;
  @Input() disabled = false;

  @Input() headerTemplate?: TemplateRef<unknown>;
  @Input() validation: IComponentFormError[] = [];
  @Input() name = '';

  @Output() valueChange = new EventEmitter<number>();

  control!: AbstractControl;
  isDisabled = false;

  ngOnInit(): void {
    this.initControl();
    this.applyDisabled();
    this.syncValueToBounds();
    this.listenToControlChanges();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['controlName'] || changes['parentForm']) {
      this.initControl();
      this.syncValueToBounds();
    }
    if (changes['disabled']) this.applyDisabled();
    if (changes['min'] || changes['max']) this.syncValueToBounds();
  }

  // ----- value helpers -----
  private rawValue(): unknown {
    return this.control?.value;
  }

  private asNumberOrNull(v: unknown): number | null {
    if (v === '' || v === null || v === undefined) return null;
    const n = Number(v);
    return Number.isFinite(n) ? n : null;
  }

  private clamp(n: number): number {
    const upMin = Math.max(n, this.min);
    return this.max == null ? upMin : Math.min(upMin, this.max);
  }

  private setValue(next: number | null, emit = true): void {
    const v = next == null ? '' : this.clamp(next);
    this.control.setValue(v, { emitEvent: emit });
    if (emit) {
      this.control.markAsDirty();
      this.control.markAsTouched();
    }
  }

  private currentNumber(): number {
    const n = this.asNumberOrNull(this.rawValue());
    return n == null ? this.min : n;
    // empty while typing shows empty in the input, but logic treats min if needed
  }

  // ----- UI actions -----
  increase(): void {
    if (!this.canIncrement()) return;
    this.setValue(this.currentNumber() + this.step);
  }

  decrease(): void {
    if (!this.canDecrement()) return;
    this.setValue(this.currentNumber() - this.step);
  }

  canDecrement(): boolean {
    if (this.isDisabled) return false;
    return this.currentNumber() - this.step >= this.min;
  }

  canIncrement(): boolean {
    if (this.isDisabled) return false;
    if (this.max == null) return true;
    return this.currentNumber() + this.step <= this.max;
  }

  // ----- keyboard & typing guards -----
  onKeyDown(e: KeyboardEvent): void {
    if (this.isDisabled) { e.preventDefault(); return; }

    // Arrow up/down act like stepper
    if (e.key === 'ArrowUp') { e.preventDefault(); this.increase(); return; }
    if (e.key === 'ArrowDown') { e.preventDefault(); this.decrease(); return; }

    const allowedNav = ['Backspace', 'Delete', 'Tab', 'ArrowLeft', 'ArrowRight', 'Home', 'End', 'Enter'];
    const ctrlCombo = (e.ctrlKey || e.metaKey) && ['a','c','v','x'].includes(e.key.toLowerCase());
    const isDigit = /^\d$/.test(e.key);

    if (allowedNav.includes(e.key) || ctrlCombo || isDigit) return;

    // Block everything else (minus, plus, dot, e/E, etc.)
    e.preventDefault();
  }

  onTyped(ev: Event): void {
    const input = ev.target as HTMLInputElement;
    const digits = this.onlyDigits(input.value);

    // Keep empty while typing; commit/clamp on blur
    if (digits === '') {
      this.setValue(null, /*emit*/false);
      // reflect sanitized string back to DOM to avoid stray chars
      if (input.value !== '') input.value = '';
      return;
    }

    // reflect sanitized value in both control and DOM
    const num = Number(digits);
    this.setValue(num, /*emit*/false);
    if (input.value !== digits) input.value = digits;
  }

  onPaste(e: ClipboardEvent): void {
    e.preventDefault();
    const text = e.clipboardData?.getData('text') ?? '';
    const digits = this.onlyDigits(text);
    const num = digits === '' ? null : Number(digits);
    this.setValue(num);
  }

  onBlur(): void {
    // When leaving the field, enforce min/max and empty => min
    const n = this.asNumberOrNull(this.rawValue());
    this.setValue(n == null ? this.min : n);
  }

  // ----- utils -----
  private onlyDigits(s: string): string {
    return s.replace(/\D+/g, '');
  }

  private initControl(): void {
    this.control = this.parentForm?.controls?.[this.controlName];
    if (!this.control) throw new Error(`Control '${this.controlName}' not found on parentForm.`);
  }

  private applyDisabled(): void {
    this.isDisabled = !!this.disabled;
    if (!this.control) return;
    if (this.isDisabled && !this.control.disabled) this.control.disable({ emitEvent: false });
    if (!this.isDisabled && this.control.disabled) this.control.enable({ emitEvent: false });
  }

  private syncValueToBounds(): void {
    if (!this.control) return;
    const n = this.asNumberOrNull(this.rawValue());
    if (n == null) return; // allow empty while typing
    const clamped = this.clamp(n);
    if (clamped !== n) this.setValue(clamped, /*emit*/false);
  }

  private listenToControlChanges(): void {
    this.control.valueChanges.subscribe(() => {
      const n = this.asNumberOrNull(this.rawValue());
      this.valueChange.emit(n == null ? this.min : n);
    });
  }

  showError(): boolean {
    return !!this.control && this.control.invalid && this.control.touched && !this.isDisabled;
  }
}
