import { Injectable } from '@angular/core';
import { ILocalData, ISessionData } from '../../interfaces';

@Injectable({
  providedIn: 'root',
})
export class StorageService {
  constructor() {}

  setLocalStorage(data: ILocalData): void {
    for (const [key, value] of Object.entries(data)) {
      if (typeof value === 'object') {
        localStorage.setItem(key, JSON.stringify(value));
      } else {
        localStorage.setItem(key, String(value));
      }
    }
  }

  setSession(data: ISessionData): void {
    if (data.permissions && data.permissions.length > 0) {
      sessionStorage.clear();
    }
    for (const [key, value] of Object.entries(data)) {
      if (typeof value === 'object') {
        sessionStorage.setItem(key, JSON.stringify(value));
      } else {
        sessionStorage.setItem(key, String(value));
      }
    }
  }

  addLocalStorageItem(key: string, value: any): void {
    if (typeof value === 'object') {
      localStorage.setItem(key, JSON.stringify(value));
    } else {
      localStorage.setItem(key, String(value));
    }
  }

  addSessionItem(key: string, value: any): void {
    if (typeof value === 'object') {
      sessionStorage.setItem(key, JSON.stringify(value));
    } else {
      sessionStorage.setItem(key, String(value));
    }
  }

  clearLocalStorage() {
    localStorage.clear();
  }

  clearSession() {
    sessionStorage.clear();
  }

  clearAll() {
    this.clearLocalStorage();
    this.clearSession();
  }

  getLocalStorageAll(): Record<string, any> {
    const localStorageData: Record<string, any> = {};
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key) {
        const value = localStorage.getItem(key);
        try {
          localStorageData[key] = JSON.parse(value as string);
        } catch {
          localStorageData[key] = value;
        }
      }
    }
    return localStorageData;
  }

  getsessionAll(): Record<string, any> {
    const sessionData: Record<string, any> = {};
    for (let i = 0; i < sessionStorage.length; i++) {
      const key = sessionStorage.key(i);
      if (key) {
        const value = sessionStorage.getItem(key);
        try {
          sessionData[key] = JSON.parse(value as string);
        } catch {
          sessionData[key] = value;
        }
      }
    }
    return sessionData;
  }

  getLocalStorageItem(item: string): string {
    return localStorage.getItem(item) ?? '';
  }

  getsessionItem(item: string | Permissions) {
    return sessionStorage.getItem(item as string);
  }
}
