import {
  Component,
  ContentChild,
  ElementRef,
  Input,
  Renderer2,
  signal,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { ClickOutsideDirective } from '../../directives';
import { NgTemplateOutlet } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
export interface IActions {
  label: string;
  icon?: string;
  class?:string;
  isDisabled?:boolean;
  callback: (ctx: any) => void;
}

@Component({
  selector: 'custom-actions-dropdown',
  imports: [ClickOutsideDirective , NgTemplateOutlet],
  templateUrl: './custom-actions-dropdown.component.html',
  styleUrl: './custom-actions-dropdown.component.css',
})
export class CustomActionsDropdownComponent {
  @Input() actions: IActions[] = [];
  @Input() context: any;
  @Input() horizontalDots: boolean = false;
  @Input() hasActionTemplate: boolean = false;
  @Input() injectedTrigger: boolean = false;
  @Input() expandSide: 'RIGHT' | 'LEFT' = 'RIGHT';
  @Input() expandDirection: 'TOP' | 'BOTTOM' = 'BOTTOM';
  @ViewChild('actionsPopup') actionsPopup?: ElementRef<HTMLDivElement>;
  @ViewChild('triggerWrapper') triggerWrapper?: ElementRef<HTMLDivElement>;
  @ViewChild('actionDropdownContainer', { read: ElementRef }) actionDropdownContainer?: ElementRef<HTMLDivElement>;
  @ContentChild('customTrigger') triggerTemplate: TemplateRef<any> | null = null;
  private isAppendedToBody = false;

  isOpen =signal<boolean>(false);
  showAnimation =signal<boolean>(false);


  constructor(
    private sanitizer: DomSanitizer,
    private renderer: Renderer2,
    private elRef: ElementRef,
      private translate: TranslateService
  ) {
      this.translate.onLangChange.pipe(takeUntilDestroyed()).subscribe(() => {
    // If dropdown is open, reposition
    if (this.isOpen()) {
      this.updatePosition();
    }
  });
  }

  sanitizeSvg(svg: string): SafeHtml {
    return this.sanitizer.bypassSecurityTrustHtml(svg);
  }
  openDropdown(): void {
    this.isOpen.set(true)
    this.showAnimation.set(false)
    setTimeout(() => this.portalToBody(), 0);
  }

  closeDropdown(): void {
    this.showAnimation.set(false)
    setTimeout(() => {
       this.isOpen.set(false)
      this.detachFromBody();
    }, 150); // Match CSS transition duration
  }
  private portalToBody(): void {
    if (!this.actionsPopup || this.isAppendedToBody) return;

    const dropdownEl = this.actionsPopup.nativeElement;
      this.renderer.setStyle(dropdownEl, 'position', 'fixed');
  this.renderer.setStyle(dropdownEl, 'top', '-9999px');
  this.renderer.setStyle(dropdownEl, 'left', '-9999px');
  this.renderer.setStyle(dropdownEl, 'visibility', 'hidden');

    this.renderer.appendChild(document.body, dropdownEl);
    this.isAppendedToBody = true;
    requestAnimationFrame(() => {
      this.updatePosition();
      requestAnimationFrame(() => {
       this.renderer.removeStyle(dropdownEl, 'visibility');
      this.showAnimation.set(true);
      });
    });
  }

private updatePosition(): void {
  if (!this.actionsPopup || !this.triggerWrapper || !this.actionDropdownContainer) return;

  const dropdownEl = this.actionsPopup.nativeElement;
  const triggerEl = this.triggerWrapper.nativeElement;
  const containerEl = this.actionDropdownContainer.nativeElement;

  const triggerRect = triggerEl.getBoundingClientRect();
  const containerRect = containerEl.getBoundingClientRect();
  const dropdownHeight = dropdownEl.offsetHeight;
  const viewportHeight = window.innerHeight;

  // Detect document direction
  const isRTL = document.dir === 'rtl';

  let top: number;
  let left: number;
  let transformOrigin: string;
  let actualDirection = this.expandDirection;

  /** ------------------------
   *  Vertical positioning
   * ------------------------ */
  if (this.expandDirection === 'BOTTOM') {
    const spaceBelow = viewportHeight - containerRect.bottom;
    if (spaceBelow >= dropdownHeight + 16) {
      top = containerRect.bottom + 8;
      transformOrigin = 'top';
    } else {
      top = containerRect.top - dropdownHeight - 8;
      transformOrigin = 'bottom';
      actualDirection = 'TOP';
    }
  } else {
    const spaceAbove = containerRect.top;
    if (spaceAbove >= dropdownHeight + 16) {
      top = containerRect.top - dropdownHeight - 8;
      transformOrigin = 'bottom';
    } else {
      top = containerRect.bottom + 8;
      transformOrigin = 'top';
      actualDirection = 'BOTTOM';
    }
  }

  /** ------------------------
   *  Horizontal positioning (RTL-aware)
   * ------------------------ */
  const expandToRight = (this.expandSide === 'RIGHT' && !isRTL) || (this.expandSide === 'LEFT' && isRTL);

  if (expandToRight) {
    left = triggerRect.right;
    transformOrigin += ' right';
    this.renderer.setStyle(dropdownEl, 'translate', '-100% 0');
  } else {
    left = triggerRect.left;
    transformOrigin += ' left';
    this.renderer.removeStyle(dropdownEl, 'translate');
  }

  /** ------------------------
   *  Apply computed styles
   * ------------------------ */
  this.renderer.setStyle(dropdownEl, 'position', 'fixed');
  this.renderer.setStyle(dropdownEl, 'top', `${top}px`);
  this.renderer.setStyle(dropdownEl, 'left', `${left}px`);
  this.renderer.setStyle(dropdownEl, 'z-index', '9999999');
  this.renderer.setStyle(dropdownEl, 'transform-origin', transformOrigin);

 // console.log('Direction:', this.expandDirection, ' Actual:', actualDirection, '| RTL:', isRTL);
}


  private detachFromBody(): void {
    if (!this.actionsPopup || !this.isAppendedToBody) return;
    const dropdownEl = this.actionsPopup.nativeElement;
    this.renderer.removeChild(document.body, dropdownEl);
    this.isAppendedToBody = false;
  }
  onClickAction(
    action: { callback: (ctx: any) => void },
    event: MouseEvent
  ): void {
    event.stopPropagation();
    action.callback(this.context);
    this.closeDropdown();
  }
  ngOnDestroy(): void {
    this.detachFromBody();
  }
}
