import { NgClass } from '@angular/common';
import {
  Component,
  EventEmitter,
  input,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import {
  debounceTime,
  distinctUntilChanged,
  Subject,
  Subscription,
} from 'rxjs';

@Component({
  selector: 'custom-reactive-search-input',
  imports: [NgClass, FormsModule , TranslateModule],
  templateUrl: './custom-reactive-search-input.component.html',
  styleUrl: './custom-reactive-search-input.component.css',
})
export class CustomReactiveSearchInputComponent {
  @Input() model: string = '';
  @Output() modelChange = new EventEmitter<string>();

  @Output() search = new EventEmitter<string>();
  headerSearchIcon = input<boolean>(false);

  @Input({ required: true }) containerClass = '';
  @Input({ required: true }) inputClass = '';
  @Input({ required: true }) inputPlaceholder = '';
  @Input() validateNumber: boolean = false;

  private inputSubject = new Subject<string>();
  constructor() {
    this.inputSubject
      .pipe(debounceTime(0), distinctUntilChanged(), takeUntilDestroyed())
      .subscribe((val) => this.search.emit(val.trim()));
  }

  onInputChange(value: string) {
    this.modelChange.emit(value); // for ngModel sync
    this.inputSubject.next(value); // for debounce emit
  }
  clearInput(): void {
    if (this.model === '') {
      this.search.emit('');
      return;
    }
    this.model = '';
    this.modelChange.emit('');
    this.search.emit('');
  }

  preventInvalidNumberInput(event: KeyboardEvent) {
    if (this.validateNumber) {
      const allowedKeys = [
        'Backspace',
        'Delete',
        'Tab',
        'Escape',
        'Enter',
        'ArrowLeft',
        'ArrowRight',
        'ArrowUp',
        'ArrowDown',
      ];

      if (allowedKeys.includes(event.key)) {
        return;
      }

      // Allow Ctrl+A, Ctrl+C, Ctrl+V, Ctrl+X
      if (
        (event.ctrlKey || event.metaKey) &&
        ['a', 'c', 'v', 'x'].includes(event.key.toLowerCase())
      ) {
        return;
      }

      // Only allow numbers (0-9) and plus sign (+)
      const validPattern = /^[0-9+]$/;
      if (!validPattern.test(event.key)) {
        event.preventDefault();
      }
    }
  }
  preventPaste(event: ClipboardEvent) {
    // if (this.validateNumber) {
    //   event.preventDefault();
    // }
  }
}
