import { CommonModule } from '@angular/common';
import {
  Component,
  computed,
  EventEmitter,
  input,
  Input,
  Output,
  signal,
  ViewChild,
} from '@angular/core';
import { IPageChangeEvent } from '../../interfaces';
import { FormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';

@Component({
  selector: 'custom-pagination',
  standalone: true,
  imports: [CommonModule, FormsModule, TranslateModule],
  templateUrl: './custom-pagination.component.html',
  styleUrls: ['./custom-pagination.component.css'],
})
export class CustomPaginationComponent {
  @Input()
  maxVisiblePages: number = 10;
  @Input() set page(value: number) {
    this._page.set(value);
  }
  _page = signal(1); // local writable signal

  get page() {
    return this._page(); // expose signal-like access
  }

  pageSize = input<number>(10);
  totalCount = input<number>(0);

  @Output() pageChange = new EventEmitter<IPageChangeEvent>();
  @Input() baseValue: number = 10;

  hideTotalCount = input<boolean>(false);
  @Input() showPageSize: boolean = true;

  pageSizeOptions = computed(() => {
    // depends on baseValue only
    return Array.from({ length: 4 }, (_, i) => this.baseValue * Math.pow(2, i));
  });

  totalPageCount = computed(() =>
    Math.ceil(this.totalCount() / this.pageSize())
  );

  totalPages = computed(() => {
    const pageCount = Math.ceil(this.totalCount() / this.pageSize());
    const pages: number[] = [];

    if (pageCount <= this.maxVisiblePages) {
      for (let i = 1; i <= pageCount; i++) {
        pages.push(i);
      }
    } else {
      const half = Math.floor(this.maxVisiblePages / 2);
      let start = Math.max(this.page - half, 1);
      let end = start + this.maxVisiblePages - 1;

      if (end > pageCount) {
        end = pageCount;
        start = Math.max(end - this.maxVisiblePages + 1, 1);
      }

      for (let i = start; i <= end; i++) {
        pages.push(i);
      }
    }

    return pages;
  });

  prevPage() {
    if (this.page > 1) {
      // this._page.set(this.page - 1);
      this.page--;
      // this.calculateTotalPages(); //  recalculate visible range
      this.pageChange.emit({
        page:
          this.page <= this.totalPageCount()
            ? this.page
            : this.totalPageCount(),
        pageSize: this.pageSize(),
      });
    }
  }

  nextPage() {
    const pageCount = Math.ceil(this.totalCount() / this.pageSize());
    if (this.page < pageCount) {
      this.page++;
      // this.calculateTotalPages(); //  update
      this.pageChange.emit({
        page:
          this.page <= this.totalPageCount()
            ? this.page
            : this.totalPageCount(),
        pageSize: this.pageSize(),
      });
    }
  }
  changePage(index: number) {
    this.page = index;
    // this.calculateTotalPages(); //  update visible pagination window
    this.pageChange.emit({
      page:
        this.page <= this.totalPageCount() ? this.page : this.totalPageCount(),
      pageSize: this.pageSize(),
    });
  }

  firstPage() {
    if (this.page > 1) {
      this.page = 1;
      // this.calculateTotalPages();
      this.pageChange.emit({
        page:
          this.page <= this.totalPageCount()
            ? this.page
            : this.totalPageCount(),
        pageSize: this.pageSize(),
      });
    }
  }

  lastPage() {
    const pageCount = Math.ceil(this.totalCount() / this.pageSize());
    if (this.page < pageCount) {
      this.page = pageCount;
      // this.calculateTotalPages();
      this.pageChange.emit({
        page:
          this.page <= this.totalPageCount()
            ? this.page
            : this.totalPageCount(),
        pageSize: this.pageSize(),
      });
    }
  }

  onPageSizeChange(event: Event) {
    const selectElement = +(event.target as HTMLSelectElement).value;
    const maxPageNum = Math.ceil(this.totalCount() / selectElement);
    this.pageChange.emit({
      page:
        this.page <= maxPageNum
          ? this.page >= 1
            ? this.page
            : 1
          : maxPageNum >= 1
          ? maxPageNum
          : 1,
      pageSize: selectElement,
    });
  }

  get startItem(): () => number {
    return () => {
      return (this.page - 1) * this.pageSize() + 1;
    };
  }

  get endItem(): () => number {
    return () => {
      const end = this.page * this.pageSize();
      return end > this.totalCount() ? this.totalCount() : end;
    };
  }
}
