import { Component, Input } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';

type StatusKey =
  | 'paid'
  | 'not_paid'
  | 'active'
  | 'inactive'
  | 'deleted'
  | 'open'
  | 'inprogress'
  | 'closed'
  | 'running'
  | 'incoming'
  | 'default'
  | 'past';

interface StyleDef {
  color: string;
  bg?: string;
  border?: string;
  label: string;
}

const STYLES: Record<StatusKey, StyleDef> = {
  paid: { color: '#12AB6C', bg: '#e4f2ec', border: '#12AB6C', label: 'PAID' },
  not_paid: {
    color: '#B42318',
    bg: '#f8e9e8',
    border: '#B42318',
    label: 'NOT PAID',
  },
  active: {
    color: '#12AB6C',
    bg: '#e4f2ec',
    border: '#12AB6C',
    label: 'Active',
  },
  inactive: {
    color: '#B42318',
    bg: '#f8e9e8',
    border: '#B42318',
    label: 'Inactive',
  },
  deleted: {
    color: '#B42318',
    bg: '#f8e9e8',
    border: '#B42318',
    label: 'Deleted',
  },

  inprogress: {
    color: '#ffffff',
    bg: '#1F1F1F',
    border: '#1F1F1F',
    label: 'In Progress',
  },
  closed: {
    color: '#ffffff',
    bg: '#D9DBE1',
    border: '#D9DBE1',
    label: 'Closed',
  },
  open: { color: '#ffffff', bg: '#602650', border: '#602650', label: 'Open' },
  running: {
    color: '#FFFFFF',
    bg: '#602650',
    border: '#602650',
    label: 'Running',
  },
  incoming: {
    color: '#25c7bc',
    bg: '#eafaf9',
    border: '#eafaf9',
    label: 'Incoming',
  },
  past: {
    color: '#1F1F1F',
    bg: '#e9e9e9',
    border: '#e9e9e9',
    label: 'Past',
  },
  default: {
    color: '#1F1F1F',
    bg: '#e9e9e9',
    border: '#e9e9e9',
    label: 'Default',
  },
};

@Component({
  selector: 'custom-status-label',
  imports: [TranslateModule],
  templateUrl: './custom-status-label.component.html',
  styleUrl: './custom-status-label.component.css',
})
export class CustomStatusLabelComponent {
  @Input({ required: true }) status!: string;

  @Input() uppercase = false;
  @Input() extraText: string = '';

  private normalize(val: string): StatusKey {
    const status = (val ?? '').trim().toLowerCase().replace(/\s+/g, '_');
    const map: Partial<Record<string, StatusKey>> = {
      paid: 'paid',
      not_paid: 'not_paid',
      not_payed: 'not_paid',
      active: 'active',
      inactive: 'inactive',
      deleted: 'deleted',
      open: 'open',
      inprogress: 'inprogress',
      'in-progress': 'inprogress',
      'in progress': 'inprogress',
      closed: 'closed',
      running: 'running',
      incoming: 'incoming',
      past: 'past',
      default: 'default',
    };
    return (map[status] ?? 'inactive') as StatusKey;
  }

  get key(): StatusKey {
    return this.normalize(this.status);
  }
  get s(): StyleDef {
    return STYLES[this.key];
  }
}
