import {
  Component,
  EventEmitter,
  Input,
  Output,
  signal,
  input,
  effect,
} from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { CustomInputFormComponent } from '../custom-input-form/custom-input-form.component';
import { CustomCalenderFormComponent } from '../custom-calender-form/custom-calender-form.component';
import { IDropdownOption } from '../../interfaces';
import { CustomMultiSelectFormComponent } from '../custom-multi-select-form/custom-multi-select-form.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { JsonPipe, NgClass } from '@angular/common';
import { Subject, takeUntil } from 'rxjs';
import { CustomDropdownFormComponent } from '../custom-dropdown-form/custom-dropdown-form.component';
import { CustomSvgIconComponent } from '../custom-svg-icon/custom-svg-icon.component';

export interface IDynamicFilterConfig {
  id: string;
  label: string;
  type: 'text' | 'date' | 'multiselect' | 'singleselect';
  placeholder?: string;
  options?: IDropdownOption[];
  collapsed?: boolean;
  icon?: string;
  validation?: {
    required?: boolean;
    minDate?: Date;
    maxDate?: Date;
  };
}

@Component({
  selector: 'custom-filter-dynamic-form',
  imports: [
    ReactiveFormsModule,
    CustomInputFormComponent,
    CustomCalenderFormComponent,
    CustomMultiSelectFormComponent,
    CustomDropdownFormComponent,
    NgClass,
  ],
  templateUrl: './custom-filter-dynamic-form.component.html',
  styleUrl: './custom-filter-dynamic-form.component.css',
})
export class CustomFilterDynamicFormComponent {
  config = input<IDynamicFilterConfig[]>([]);
  values = input<Record<string, any> | null>(null);
  @Output() formChanged = new EventEmitter<any>();
  @Output() formReset = new EventEmitter<any>();

  collapseState = signal<Record<string, boolean>>({});
  filterForm!: FormGroup;
  filters = signal<IDynamicFilterConfig[]>([]);
  private destroy$ = new Subject<void>();
  constructor(private fb: FormBuilder) {
    // effect(() => {
    //   if (this.values()) {
    //      this.filterForm = this.buildForm(this.config() , this.values());
    //   }
    // });
  }
  update = 0;

  ngOnInit() {
    const state: Record<string, boolean> = {};
    this.config().forEach((field) => {
      state[field.id] = field.collapsed ?? false;
    });

    this.collapseState.set(state);

    this.filterForm = this.buildForm(this.config(), this.values());

    this.filterForm.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe((val) => {
        if (this.update <= 1) {
          this.update++;
        } else {
          console.log('Form Values Changed:', val);
          const payload = this.buildEmittableValues(this.config(), val);
          const baseline = this.buildEmittableValues(
            this.config(),
            this.values() ?? {}
          );

          if (!this.isSame(payload, baseline)) this.formChanged.emit(payload);
        }
      });
  }

  toggleCollapse(id: string) {
    const state = { ...this.collapseState() };
    state[id] = !state[id];
    this.collapseState.set(state);
  }

  private buildForm(
    config: IDynamicFilterConfig[],
    initialValues?: Record<string, any> | null
  ): FormGroup {
    const group = this.fb.group({});

    for (const field of config) {
      const validators = this.buildValidators(field);
      let initial = initialValues?.[field.id] ?? null;

      // Detect if the field is a date field
      const isDateField =
        field.type === 'date' || field.id.toLowerCase().includes('date');

      if (isDateField && initial) {
        // Convert string or timestamp to Date
        const parsedDate = new Date(initial);
        if (!isNaN(parsedDate.getTime())) {
          initial = parsedDate;
        } else {
          console.warn(`Invalid date value for ${field.id}:`, initial);
          initial = null;
        }
      }

      group.addControl(field.id, this.fb.control(initial, validators));
    }

    return group;
  }
  private isSame(a: any, b: any): boolean {
    const normalize = (obj: any) =>
      JSON.stringify(obj, (key, value) => {
        if (value instanceof Date) {
          return value.toISOString();
        }
        return value;
      });
    return normalize(a) === normalize(b);
  }
  private buildEmittableValues(
    config: IDynamicFilterConfig[],
    raw: Record<string, any>
  ): Record<string, any> {
    const out: Record<string, any> = {};
    for (const field of config) {
      const value = raw?.[field.id];
      out[field.id] = this.isDateField(field)
        ? this.toYyyyMmDd(value) // convert only date fields
        : value;
    }
    return out;
  }

  private isDateField(field: IDynamicFilterConfig): boolean {
    return field.type === 'date' || field.id.toLowerCase().includes('date');
  }

  private toYyyyMmDd(value: any): string | null {
    if (value == null || value === '') return null;
    const d = value instanceof Date ? value : new Date(value);
    if (isNaN(d.getTime())) return null;
    const day = String(d.getDate()).padStart(2, '0');
    const month = String(d.getMonth() + 1).padStart(2, '0');
    const year = d.getFullYear();
    return `${year}-${month}-${day}`;
  }

  private buildValidators(field: IDynamicFilterConfig) {
    const v = [];
    if (field.validation?.required) v.push(Validators.required);
    return v;
  }

  get form(): FormGroup {
    return this.filterForm;
  }
  reset() {
    this.form.reset();
    this.formReset.emit(true);
  }

  control(name: string): AbstractControl | null {
    return this.filterForm.get(name);
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
