import { Injectable, signal } from '@angular/core';

export type ToastType = 'success' | 'warning' | 'black' | 'error' | 'info';
export type ToastPosition =
  | 'top-right'
  | 'top-left'
  | 'bottom-right'
  | 'bottom-left'
  | 'top-center'
  | 'bottom-center';
export interface ToastItem {
  id: string;
  message: string;
  position: ToastPosition;
  type: ToastType;
  duration: number; // ms
  state: 'entering' | 'shown' | 'leaving';
  createdAt: number;
}

let __toastIdCounter = 0;
function genId(): string {
  __toastIdCounter = (__toastIdCounter + 1) % 1_000_000;
  return (
    Date.now().toString(36) +
    '-' +
    __toastIdCounter.toString(36).padStart(4, '0') +
    '-' +
    Math.random().toString(36).slice(2, 8)
  );
}


@Injectable({
  providedIn: 'root',
})
export class ToastService {
  message = signal('Default message');
  type = signal<ToastType>('info');
  position = signal<ToastPosition>('top-right');
  show = signal(false);
  // === Private queue state ===
  readonly visibleToasts = signal<ToastItem[]>([]);
   private readonly maxStackPerPosition = 4; // tweak as you like
  private readonly leaveMs = 200;

  toast(
    message: string,
    position: ToastPosition,
    ToastType: ToastType = 'info',
    duration?: number
  ) {
    const item: ToastItem = {
      id: genId(),
      message,
      position,
      type: ToastType,
      duration: this.normalizeDuration(duration),
      state: 'entering',
      createdAt: Date.now(),
    };

    this.pushIntoStack(item);            // <-- stacked behavior
    this.animateEnter(item);             // enter -> shown -> schedule auto-hide

    // keep old single-toast signals in sync
    this.message.set(message);
    this.type.set(ToastType);
    this.position.set(position);
    this.show.set(true);
  }

  // public close API
  close(id: string) {
    this.setState(id, 'leaving');
    setTimeout(() => this.remove(id), this.leaveMs);
  }

  // ---------- small helpers ----------
  private normalizeDuration(d?: number) {
    return typeof d === 'number' && d > 0 ? d : 2000;
  }

  private pushIntoStack(item: ToastItem) {
    this.visibleToasts.update((list) => {
      // newest on top for that position
      const samePos = list.filter((t) => t.position === item.position);
      const others = list.filter((t) => t.position !== item.position);

      // enforce max stack per position
      const trimmed = samePos.length >= this.maxStackPerPosition
        ? samePos
            .sort((a, b) => a.createdAt - b.createdAt) // oldest first
            .slice(1) // drop oldest one
        : samePos;

      // put the new one on top
      return [...others, item, ...trimmed.filter(Boolean)];
    });
  }

  private animateEnter(item: ToastItem) {
    // let DOM paint, then set shown (CSS handles the stagger)
    requestAnimationFrame(() => {
      this.setState(item.id, 'shown');

      // schedule auto-hide
      const timer = setTimeout(() => {
        this.setState(item.id, 'leaving');
        setTimeout(() => {
          this.remove(item.id);
          clearTimeout(timer);
        }, this.leaveMs);
      }, item.duration);
    });
  }

  private setState(id: string, state: ToastItem['state']) {
    this.visibleToasts.update((list) =>
      list.map((t) => (t.id === id ? { ...t, state } : t))
    );
  }

  private remove(id: string) {
    this.visibleToasts.update((list) => list.filter((t) => t.id !== id));
    if (this.visibleToasts().length === 0) this.show.set(false);
  }

  // keep originals
  showToast() { this.show.set(true); }
  hideToast() { this.show.set(false); }
}
