import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'custom-radio-component',
  imports: [FormsModule],
  templateUrl: './custom-radio-component.component.html',
  styleUrl: './custom-radio-component.component.css'
})
export class CustomRadioComponentComponent {
  @Input() radioClass: string = '';
  @Input({ required: true }) name: string = '';
  @Input({ required: true }) value: any;
  @Input() label: string = '';
  @Input() disabled: boolean = false;

  // Changed from checked to model
  @Input() model: any;
  @Output() modelChange = new EventEmitter<any>();

  get isChecked(): boolean {
    return this.model === this.value;
  }

  onInputChange() {
    if (this.disabled) return;
    this.modelChange.emit(this.value);
  }
}
