import { trigger, transition, style, animate } from '@angular/animations';
import { Directive, HostBinding, Input } from '@angular/core';

export const dropdownAnimation = trigger('dropdown', [
  transition(':enter', [
    style({ height: '0', opacity: 0, overflow: 'hidden' }),
    animate('250ms ease-out', style({ height: '*', opacity: 1 })),
  ]),
  transition(':leave', [
    style({ height: '*', opacity: 1, overflow: 'hidden' }),
    animate('200ms ease-in', style({ height: '0', opacity: 0 })),
  ]),
]);

@Directive({
  selector: '[DropdownAnimationObject]',
  standalone: true,
  host: {
    '[@dropdown]': 'isOpen',
  },
})
export class DropdownsAnimationDirective {
  @Input('DropdownAnimationObject') isOpen = false;

  // HostBinding attaches the trigger
  @HostBinding('@dropdown') get dropdown() {
    return this.isOpen ? true : false;
  }
}

