import { Inject, Injectable, InjectionToken } from '@angular/core';
import { catchError, Observable, retry } from 'rxjs';
import { ILoginData, IUserData } from '../../interfaces';
import { SKIP_TOKEN } from '../../utilities/http-context';
import { HttpClient, HttpContext } from '@angular/common/http';
import { IGlobalAuthResponse } from '../../interfaces/auth/gloal-res.interface';
import { AuthContextService } from './auth-context.service';
import { Router } from '@angular/router';
import { API_BASE_URL } from './base-url-token';



@Injectable({
  providedIn: 'root',
})
export class AuthBeService {
  constructor(
    private http: HttpClient,
    private authContextService: AuthContextService,
    private router: Router,
    @Inject(API_BASE_URL) private baseUrl: string
  ) {}

  login(data: {
    username: string;
    password: string;
  }): Observable<IGlobalAuthResponse<ILoginData>> {
    return this.http.post<IGlobalAuthResponse<ILoginData>>(
     `${this.baseUrl}/api/v1/idm/auth/login`,
      { username: data.username, password: data.password },
      {
        context: new HttpContext().set(SKIP_TOKEN, true),
      }
    );
  }

  logout(): Observable<IGlobalAuthResponse<any>> {
    alert(`${this.baseUrl}/api/v1/idm/auth/logout`)
    return this.http.post<IGlobalAuthResponse<any>>(
     `${this.baseUrl}/auth/logout`,
      {}
    );
  }

  refreshToken(refreshToken: {
    refreshToken: string;
  }): Observable<IGlobalAuthResponse<ILoginData>> {
    return this.http.post<IGlobalAuthResponse<ILoginData>>(
       `${this.baseUrl}/api/v1/idm/auth/refresh`,
      refreshToken,
      {
        context: new HttpContext().set(SKIP_TOKEN, true),
      }
    );
  }

  validateToken(): Observable<IGlobalAuthResponse<any>> {
    return this.http
      .post<IGlobalAuthResponse<any>>(
        `${this.baseUrl}/api/v1/idm/auth/validate`,
        {}
      )
      .pipe(
        retry(3),
        catchError((error) => {
          console.error('Request failed after 3 retries', error);
          this.authContextService.clearData();
          window.dispatchEvent(new CustomEvent('auth-logout'));
          this.router.navigate(['/auth']);
          throw error;
        })
      );
  }

  getCurrUser(): Observable<IGlobalAuthResponse<IUserData>> {
    return this.http.get<IGlobalAuthResponse<IUserData>>(
      `${this.baseUrl}/auth/me`
    );
  }
}
