import { Component, Input } from '@angular/core';
import { CustomButtonComponent } from '../custom-button/custom-button.component';
import { TranslateModule } from '@ngx-translate/core';

@Component({
  selector: 'custom-place-holder',
  imports: [TranslateModule],
  templateUrl: './custom-place-holder.component.html',
  styleUrl: './custom-place-holder.component.css',
})
export class CustomPlaceHolderComponent {
  @Input({ required: true }) title: string = 'No Data Available'; // No Visits Requested Yet
  @Input({ required: true }) description: string =
    'There is no data to display at the moment.'; //  You havent created any visitor requests. Start by creating your first visit and adding your visitors details.
}
