import {
  Component,
  EventEmitter,
  Input,
  model,
  ModelSignal,
  Output,
} from '@angular/core';
import {
  CustomCalenderFormComponent,
  CustomMultiSelectFormComponent,
  CustomInputFormComponent,
  CustomDropdownComponent,
} from '..';
import {
  FormBuilder,
  FormGroup,
  FormControl,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { CommonModule } from '@angular/common';
import { IComponentFormError, IDropdownOption } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { TranslateModule } from '@ngx-translate/core';
import { filter } from 'rxjs';
//
export type FilterType = 'multiselect' | 'date' | 'text' | 'dateRange';

export interface FilterConfig extends IDropdownOption {
  type: FilterType;
  label: string;
  options?: IDropdownOption[];
  placeholder?: string;
  constraints?: IPossibleConstrains;
  reset: boolean;
  validation: IComponentFormError[];
  disabled: boolean;
}

export interface IPossibleConstrains {
  minDate?: Date;
  maxDate?: Date;
  minVal?: number;
}

export type FilterValues = Record<string, any>;

export type FilterRow = {
  filterId?: string | number;
  filterConfig?: FilterConfig;
  controlName: string;
};

@Component({
  selector: 'custom-filter-dropdown',
  imports: [
    CommonModule,
    CustomInputFormComponent,
    CustomCalenderFormComponent,
    CustomMultiSelectFormComponent,
    ReactiveFormsModule,
    CustomDropdownComponent,
    TranslateModule,
  ],
  templateUrl: './custom-filter-dropdown.component.html',
  styleUrl: './custom-filter-dropdown.component.css',
})
export class CustomFilterDropdownComponent {
  @Input({ required: true }) filtersConfig: FilterConfig[] = [];
  @Output() filtersChanged: EventEmitter<FilterValues> =
    new EventEmitter<FilterValues>();

  activeFilters: ModelSignal<{
    main: FilterRow[];
    filteredValues: FilterValues;
  }> = model({
    main: [] as FilterRow[],
    filteredValues: {} as FilterValues,
  });

  filterOptions: IDropdownOption[] = [];
  filterForm!: FormGroup;

  emptyOptions: IDropdownOption[] = [] as IDropdownOption[];
  nullValidation: IComponentFormError[] = {} as IComponentFormError[];
  protected readonly errorConstant = ComponentFormErrorConstant;

  constructor(private fb: FormBuilder) {
    this.filterOptions = [];
  }

  ngOnInit() {
    this.filterOptions = this.filtersConfig.map((f) => ({
      id: f.id,
      nameEn: f.nameEn,
      nameAr: f.nameAr,
    }));

    this.filterForm = this.fb.group({});

    const { main, filteredValues } = this.activeFilters();

    main.forEach((row) => {
      if (row.filterConfig) {
        const initialValue =
          filteredValues[row.filterConfig.label] ??
          this.getDefaultInitialValue(row.filterConfig.type);

        const validators = this.getValidatorsFromConfig(
          row.filterConfig.validation
        );
        if (row.filterConfig.type === 'date') {
          const dateValue = new Date(initialValue);

          this.filterForm.addControl(
            row.filterConfig.label,
            new FormControl(dateValue, validators)
          );
        } else {
          this.filterForm.addControl(
            row.filterConfig.label,
            new FormControl(initialValue, validators)
          );
        }
      }
    });

    this.filterForm.valueChanges
      .pipe(
        filter((val) => !this.isFilterFormEmpty()) // only run if there's data
      )
      .subscribe((val) => {
        const transformed = { ...val };
        Object.keys(transformed).forEach((key) => {
          const cfg = this.filtersConfig.find((f) => f.label === key);
          if (cfg?.type === 'date' && transformed[key] instanceof Date) {
            transformed[key] = transformed[key].toISOString().split('T')[0];
          }
        });
        this.activeFilters.update((current) => ({
          ...current,
          filteredValues: transformed,
        }));
        this.filtersChanged.emit(this.activeFilters().filteredValues);
      });
  }

  getDefaultInitialValue(type: FilterType) {
    switch (type) {
      case 'multiselect':
        return [];
      case 'date':
        return null;
      default:
        return '';
    }
  }

  getValidatorsFromConfig(validations: IComponentFormError[] = []) {
    const validators = [];

    validators.push(Validators.required);
    return validators;
  }

  getAvailableOptions(activeIndex: number): IDropdownOption[] {
    const selectedIds = this.activeFilters()
      .main.filter((row, idx) => row.filterId && idx !== activeIndex)
      .map((row) => row.filterId);
    return this.filterOptions.filter((opt) => !selectedIds.includes(opt.id));
  }

  addFilterRow() {
    this.activeFilters.update((current) => ({
      ...current,
      main: [...current.main, { controlName: `row_${Date.now()}` }],
    }));
  }

  onFilterDropdownChange(selectedId: any, rowIndex: number) {
    if (!selectedId) return;

    const filterConfig = this.filtersConfig.find((f) => f.id === selectedId.id);
    if (!filterConfig) return;

    const row = this.activeFilters().main[rowIndex];

    // Remove old control if it exists
    if (row.filterConfig && this.filterForm.contains(row.filterConfig.label)) {
      this.filterForm.removeControl(row.filterConfig.label);

      // Also remove from filteredValues
      this.activeFilters.update((current) => {
        const { [row.filterConfig!.label]: _, ...rest } =
          current.filteredValues;
        return { ...current, filteredValues: rest };
      });
    }

    // Assign new filter configuration
    row.filterId = selectedId.id;
    row.filterConfig = filterConfig;

    // Add a control for the selected filter type
    let control: FormControl;
    switch (filterConfig.type) {
      case 'multiselect':
        control = new FormControl([], [Validators.required]);
        break;
      case 'date':
        control = new FormControl(null);
        break;
      default:
        control = new FormControl('');
        break;
    }

    this.filterForm.addControl(filterConfig.label, control);
  }

  removeFilterRow(rowIndex: number) {
    const row = this.activeFilters().main[rowIndex];

    if (
      row &&
      row.filterConfig &&
      this.filterForm.contains(row.filterConfig.label)
    ) {
      this.filterForm.get(row.filterConfig.label)?.reset();
      this.filterForm.removeControl(row.filterConfig.label);
    }

    this.activeFilters().main.splice(rowIndex, 1);
    if (this.isFilterFormEmpty()) {
      this.filtersChanged.emit(this.activeFilters().filteredValues);
    }
  }

  isFilterFormEmpty(): boolean {
    const val = this.filterForm.value;

    return !Object.values(val).some((v) => {
      if (v === null || v === undefined) return false;
      if (typeof v === 'string') return v.trim() !== '';
      if (Array.isArray(v)) return v.length > 0;
      return true;
    });
  }

  clearAll() {
    this.filterForm.reset();
    this.activeFilters.update((current) => ({
      ...current,
      filteredValues: this.filterForm.value,
      main: [],
    }));
    console.log();
    this.filtersChanged.emit(this.activeFilters().filteredValues);
  }

  onDateChange(event: any, name: string) {
    console.log('hi', event, name);
  }

  onMultiSelectChange(event: any, name: string) {
    console.log('hi', event, name);
  }

  onDropdownChange(event: any, name: string) {
    console.log('hi', event, name);
  }

  filtersChangedEmit() {
    this.activeFilters.update((current) => ({
      ...current,
      filteredValues: this.filterForm.value,
    }));
    this.filtersChanged.emit(this.activeFilters().filteredValues);
  }
}
