import { isPlatformBrowser } from '@angular/common';
import {
  AfterViewInit,
  ElementRef,
  EventEmitter,
  Inject,
  NgZone,
  OnDestroy,
  Output,
  PLATFORM_ID,
} from '@angular/core';
import { Directive, Input } from '@angular/core';
import { Subscription, filter, fromEvent } from 'rxjs';

@Directive({
  selector: '[clickOutside]',
})
export class ClickOutsideDirective implements AfterViewInit, OnDestroy {
  constructor(
    private readonly elementReference: ElementRef<HTMLElement>,
    private readonly zone: NgZone,
    @Inject(PLATFORM_ID) private platform: object
  ) {}
  @Input() clickOutside!: HTMLElement;
  @Output() clickOutsideEmitter = new EventEmitter<void>();
  isFirstTime: boolean = true;
  private destroySubscription = new Subscription();
  ngAfterViewInit(): void {
    if (isPlatformBrowser(this.platform)) {
      // Delay subscription to avoid catching the opening click
      setTimeout(() => {
        this.zone.runOutsideAngular(() => {
          const documentClick$ = fromEvent<MouseEvent>(document, 'click');
          const subscription = documentClick$
            .pipe(
              filter((event) => {
                // Only emit if click is outside the host element
                return !this.elementReference.nativeElement.contains(
                  event.target as Node
                );
              })
            )
            .subscribe(() => {
              this.zone.run(() => {
                this.clickOutsideEmitter.emit();
              });
            });
          this.destroySubscription = subscription;
        });
      }, 0);
    }
  }

  ngOnDestroy(): void {
    this.destroySubscription.unsubscribe();
  }
}
