import {
  ComponentFormErrorConstant,
  IComponentFormError,
} from '../../public-api';
import { TranslateService } from '@ngx-translate/core';

export function getErrorValidation(
  fieldLabelKey: string,
  errorTypes: { error: ComponentFormErrorConstant; extra?: string }[],
  translate: TranslateService
): IComponentFormError[] {
  return errorTypes.map((type) => {
    let message = '';
    switch (type.error) {
      case ComponentFormErrorConstant.REQUIRED:
        message =
          fieldLabelKey + translate.instant('GENERAL.VALIDATION.REQUIRED');
        break;
      case ComponentFormErrorConstant.MINLENGTH:
        message =
          fieldLabelKey +
          translate.instant('GENERAL.VALIDATION.MINLENGTH') +
          type.extra;
        break;
      case ComponentFormErrorConstant.MAXLENGTH:
        message =
          fieldLabelKey +
          translate.instant('GENERAL.VALIDATION.MAXLENGTH') +
          type.extra;
        break;
      case ComponentFormErrorConstant.PATTERN:
        message =
          fieldLabelKey +
          translate.instant('GENERAL.VALIDATION.PATTERN') +
          type.extra;
        break;
      case ComponentFormErrorConstant.EMAIL:
        message = fieldLabelKey + translate.instant('GENERAL.VALIDATION.EMAIL');
        break;
      case ComponentFormErrorConstant.MIN:
        message =
          fieldLabelKey +
          translate.instant('GENERAL.VALIDATION.MIN') +
          type.extra;
        break;
      case ComponentFormErrorConstant.MAX:
        message =
          fieldLabelKey +
          translate.instant('GENERAL.VALIDATION.MAX') +
          type.extra;
        break;
      default:
        message = fieldLabelKey;
    }
    return {
      errorType: [type.error],
      errorMessage: message,
    } as IComponentFormError;
  });
}
