import { Component, computed, inject } from '@angular/core';
import { NgClass, NgIf, NgFor } from '@angular/common';
import { ToastItem, ToastPosition, ToastService } from '../../services';
import { TranslateModule } from '@ngx-translate/core';
@Component({
  selector: 'custom-toast-viewport',
  imports: [NgClass, TranslateModule],
  templateUrl: './custom-toast-viewport.component.html',
  styleUrl: './custom-toast-viewport.component.css'
})
export class CustomToastViewportComponent {
private toast = inject(ToastService);

  positions: ToastPosition[] = [
    'top-left',
    'top-center',
    'top-right',
    'bottom-left',
    'bottom-center',
    'bottom-right',
  ];


  toastsBy(pos: ToastPosition): ReadonlyArray<ToastItem> {
    // newest on top for that position
    return this.toast
      .visibleToasts()
      .filter(t => t.position === pos)
      .sort((a, b) => b.createdAt - a.createdAt);
  }

  posClass(pos: ToastPosition) {
    switch (pos) {
      case 'top-right': return 'toast-top-right';
      case 'top-left': return 'toast-top-left';
      case 'bottom-right': return 'toast-bottom-right';
      case 'bottom-left': return 'toast-bottom-left';
      case 'top-center': return 'toast-top-center';
      case 'bottom-center': return 'toast-bottom-center';
    }
  }

  close(id: string) { this.toast.close(id); }
}
