/* eslint-disable @typescript-eslint/no-explicit-any */
import { isPlatformBrowser } from '@angular/common';
import { HttpErrorResponse, HttpInterceptorFn } from '@angular/common/http';
import { inject, PLATFORM_ID } from '@angular/core';
import {
  catchError,
  mergeMap,
  retry,
  retryWhen,
  throwError,
  timer,
} from 'rxjs';
import { AuthContextService, AuthService, ToastService } from '../../services';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

export const ErrorInterceptor: HttpInterceptorFn = (req, next) => {
  let authService = inject(AuthService);
  let authContextService = inject(AuthContextService);
  let router = inject(Router);
  let toastService = inject(ToastService);
  const translateService = inject(TranslateService);
  return next(req).pipe(
    catchError((error: HttpErrorResponse) => {
      const lang = translateService.currentLang || 'en';
      const suffix = lang.charAt(0).toUpperCase() + lang.slice(1);
      switch (error.status) {
        case 400:
          if (error.error.errorMessage) {
            toastService.toast(
              error.error.errorMessage,
              'top-center',
              'error',
              2000
            );
            break;
          } else if (error.error.errors && error.error.errors.length > 0) {
            error.error.errors.forEach((err: any) => {
              const msg =
                err[`message${suffix}`] ||
                err.message ||
                err.messageEn ||
                'Unexpected error';
              toastService.toast(msg, 'top-center', 'error', 2000);
            });
            break;
          } else {
            const msg =
              error.error[`message${suffix}`] ||
              error.error.errorMessage ||
              error.error.messageEn ||
              'Unexpected error';
            toastService.toast(msg, 'top-center', 'error', 2000);
            break;
          }
        case 401:
          // access token expired / au auth
          authService.handleRefreshToken();
          //  authContextService.clearData();
          // window.dispatchEvent(new CustomEvent('auth-logout'));
          // router.navigate(['/auth/login']);
          break;
        case 403:
          // no permission
          toastService.toast(
            `User don't have permission`,
            'top-center',
            'error',
            2000
          );
          authService.handlePermissionConfig();
          console.error('No Permission');
          break;

        case 406:
          // refresh expired
          authContextService.clearData();
          window.dispatchEvent(new CustomEvent('auth-logout'));
          router.navigate(['/auth']);
          break;
        case 404:
          console.error('End Point Not Found');
          break;
        case 503:
          toastService.toast(
            `Please Contact Support Team`,
            'top-center',
            'error',
            2000
          );
          break;
        default:
          toastService.toast(
            `Please Contact Support Team`,
            'top-center',
            'error',
            2000
          );
      }
      // refresh token + code
      // access token
      // 503 -- service idle -- error message
      // 404 -- end point not found // /

      // if (error.error && isPlatformBrowser(PLATFORM_ID)) {
      //   if (error.error.errors) {
      //     const errorMessages = Object.values(error.error.errors).flat();
      //     errorMessages.forEach((errorMessage: any) => {
      //       console.error(errorMessage);
      //     });
      //   } else if (error?.error?.message) {
      //     console.error(error.error.message);
      //   } else {
      //     console.error('Something went wrong');
      //   }
      // }
      return throwError(error);
    })
  );
};
